<template>
    <div class="card">
        <div class="cardTitle">
            <div>{{ title }}</div>
            <div class="btnGroup">
                <buttonLib class="btn" :isActive="N==7" text="7日" @click="getData(7)"/>
                <buttonLib class="btn" :isActive="N==30" text="30日" @click="getData(30)"/>
                <buttonLib class="btn" text="刷新" @click="getData(N)"/>
            </div>
        </div>
        <div :id="name" :class="data?.x?.length == 0?'chartBox loadingLocal':'chartBox'"></div>
    </div>
</template>

<script>
import * as echarts from 'echarts';
import axios from 'axios';
import buttonLib from '@/components/lib/unit/buttonLib.vue';

export default {
    name: 'chartBox',
    components:{
        buttonLib
    },
    props:{
        name:{
            type:String,
            default:function(){
                return "chartBox"
            }
        },
        title:{
            type:String,
            default:function(){
                return "图表"
            }
        }
    },
    data(){
        return {
            URL:window.config.apiUrl,
            data:{
                "x" : [],
                "y" : [],
            },
            N:30,
            axiosC:new AbortController(),    //请求列表 用于取消请求
        }
    },
    methods:{
        //获取数据
        async getData(n=30){
            this.N = n;
            let token = this.$root.getLocalToken();
            this.data = {
                "x" : [],
                "y" : [],
            };
            if(!token){
                return;
            }
            try{
                let res = await axios.get(this.URL+"/frp/traffic?n="+n,
                    {
                        headers:{token:token},
                        signal: this.axiosC.signal
                    }
                );
                if(res?.data?.code == 1){
                    let data = res.data.data;
                    data.forEach(element => {
                        this.data.x.push(element.date);
                        this.data.y.push(element.total);
                    });
                }
            }
            catch(err){ 
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else{
                    this.$root.netError(err);
                }                
            }
            try{
                this.createChart();
            }
            catch(err){ 
                console.log("未找到图表元素-用户可能切换了页面:");
                console.log(err)
            }
        },
        //渲染图表
        createChart(){
            let dom = document.getElementById(this.name);
            //如果dom没有宽高则退出
            if(!dom.offsetWidth || !dom.offsetHeight){
                return;
            }
            let chartInstance = echarts.getInstanceByDom(dom);
            if(!chartInstance){
                chartInstance = echarts.init(dom);
            }
            if(!chartInstance){
                return;
            }
            let chart = chartInstance;
            
            let option;

            let dataAxis = this.data?.x ?? [];
            let data = this.data?.y ?? [];
            let yMax = 500;
            let dataShadow = [];
            for (let i = 0; i < data.length; i++) {
                dataShadow.push(yMax);
            }

            option = {
                grid: {
                    top: 40,     // 距离容器顶部
                    bottom: 80,  // 距离容器底部
                    left: 0,    // 距离容器左侧
                    right: 0    // 距离容器右侧
                },
                xAxis: {
                    data: dataAxis,
                    axisTick: {
                        show: false
                    },
                    axisLine: {
                        show: false
                    },
                    z: 10
                },
                yAxis: {
                    axisLine: {
                        show: false
                    },
                    axisTick: {
                        show: false
                    },
                    axisLabel: {
                        color: '#999',
                        formatter: function (value) {
                            return (value / 1024).toFixed(0) + ' MB';  
                        }
                    }
                },
                dataZoom: [
                    {
                        type: 'inside',
                        start: this.N>=30?84:0,
                        end: 100,
                        zoomLock: true,
                    },
                    {
                        type: 'slider',
                        start: this.N>=30?84:0,
                        end: 100,
                        minSpan:this.N>=30?17:0,
                        maxSpan:this.N>=30?17:0,
                        zoomLock: true,
                        backgroundColor:"#FFF",
                        dataBackground:{
                            lineStyle: {
                                color: "#b81010",
                                width: 1,
                            },
                            areaStyle: {
                                color: "#FFF",
                            }
                        },
                        selectedDataBackground:{
                            lineStyle: {
                                color: "#000",
                                width: 2,
                            },
                            areaStyle: {
                                color: "#FFF",
                            }
                        },
                        fillerColor:"#00000080",
                        handleSize:"0%",
                        brushSelect:false,
                        moveHandleStyle:{
                            color:"#000"
                        },
                    }
                ],
                series: [
                    {
                        type: 'bar',
                        showBackground: true,
                        itemStyle: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                            { offset: 0, color: '#999' },
                            { offset: 0.5, color: '#00000080' },
                            { offset: 1, color: '#000' }
                            ])
                        },
                        emphasis: {
                            itemStyle: {
                            color: new echarts.graphic.LinearGradient(0, 0, 0, 1, [
                                { offset: 0, color: '#000' },
                                { offset: 0.7, color: '#00000080' },
                                { offset: 1, color: '#999' }
                            ])
                            }
                        },
                        label: {
                            show: true,
                            color: '#FFF',
                            fontSize: 12,
                            formatter: function (value) {
                                let mb = value?.data / 1024;   // KB 转 MB
                                return mb.toFixed(2) + ' MB'; // 保留两位小数
                            }
                        },
                        data: data
                    }
                ],
            };

            option && chart.setOption(option);
        }
    },
    mounted(){
        this.$nextTick(()=>{
            this.getData(this.N);
        })
    },
    beforeDestroy(){
        this.axiosC.abort();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/lib/chartBox.scss"></style>