<template>
    <div id="loginBox">
        <dialogLib
        ref="login"
        title='登录千鹤'
        titleSize="2rem"
        @hide="$emit('hide',true)"
        width="30vw"
        height="auto"
        maxWidth="450px"
        >
            <div class="loginForm">
                <label>用户名/邮箱</label>
                <input type="text" v-model="loginForm.username" placeholder="请输入用户名/邮箱"/>
                <label>密码</label>
                <input type="password"  v-model="loginForm.password" placeholder="请输入密码"/>  
                <div class="alerts" v-if="!hideHcaptcha">
                    完成人机验证以登录
                </div>
                <div class="code" v-if="!hideHcaptcha">
                    <vue-hcaptcha @verify="hcapResult" :sitekey=hCaptchaKey :size="hideHcaptcha?'invisible':'normal'" ></vue-hcaptcha>
                </div>
                <div class="btnGroup">
                    <div class="btn active">登录</div>
                    <div class="btn" @click="$root.goReg()">去注册</div>
                </div>
            </div>
        </dialogLib>
    </div>
</template>

<script> 
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';
import axios from 'axios';

import dialogLib from "@/components/lib/unit/dialogLib.vue";

export default{
    name:"loginBox",
    components:{
        VueHcaptcha,
        dialogLib,
    },
    data() {
        return {
            loginForm:{
                username:"",
                password:"",
                code:""
            },
            URL:window.config.apiUrl,
            hCaptchaKey : window.config.hCaptcha,
            hideHcaptcha:true,
        }
    },
    watch:{
        "loginForm":{
            handler(val){
                if(val.username!="" && val.password!=""){
                    this.hideHcaptcha = false;
                }
                else{
                    this.hideHcaptcha = true;
                }
            },
            immediate:true,
            deep:true
        }
    },
    methods:{
        //hcaptcha验证结果
        hcapResult(token){
            this.loginForm.code = token;
            if(token){
                this.hideHcaptcha = true;
                this.login();
            }
        },
        login(){
            //检查表单S
            if(this.loginForm.username=="" || this.loginForm.password==""){
                this.$notify.error({
                    title: '提示',
                    message:"请填写完整信息",
                    offset:this.$root.notifyOffet
                });
                this.hideHcaptcha = true;
                this.$nextTick(()=>{
                    this.hideHcaptcha = false;
                })
                this.loginForm = {
                    username:"",
                    password:"",
                    code:""
                };
                return;
            }
            if(this.loginForm.code==""){
                this.$notify.error({
                    title: '提示',
                    message:"请完成人机验证",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            //检查表单E
            this.$root.loading(true);
            axios.post(this.URL+"/user/login",this.loginForm)
            .then(res=>{
                let data = res.data ?? null;
                if(data.code == -1){
                    this.$notify.error({
                        title: '提示',
                        message:data.msg,
                        offset:this.$root.notifyOffet
                    });
                    this.$root.loading(false);
                    this.$root.enableMask(false);
                    this.hideHcaptcha = true;
                    this.$nextTick(()=>{
                        this.hideHcaptcha = false;
                    })
                    return;
                }
                if(data.code == -2){
                    this.$root.loading(false);
                    this.hideHcaptcha = false;
                    this.$emit('appeal',data?.msg,this.loginForm.username);
                    return;
                }
                this.$root.enableMask(false);
                this.$root.loading(false);
                //存储token
                localStorage.setItem("token",data?.data?.userinfo?.token ?? "");
                this.$notify.success({
                    title: '提示',
                    message:"登录成功",
                    offset:this.$root.notifyOffet
                });
                this.$refs?.login?.hide();
                if(this.$route.path != "/usercenter"){
                    this.$router.replace({
                        path: '/usercenter'
                    });
                }
                this.$emit('hide',true);
            })
            .catch(err=>{ 
                this.hideHcaptcha = true;
                this.$nextTick(()=>{
                    this.hideHcaptcha = false;
                })
                this.$root.enableMask(false);
                this.$root.loading(false);
                this.$emit('hide',true);
                this.$nextTick(()=>{
                    this.$root.netError(err);
                });
            })
        },
    },
}

</script>

<style lang="scss" scoped src="@/assets/css/lib/loginBox.scss">

</style>