<template>
    <div id="regBox">
        <dialogLib
        title="注册千鹤"
        titleSize="2rem"
        @hide="$emit('hide',true)"
        width="30vw"
        height="auto"
        maxWidth="450px"
        >
            <div class="regForm">
                <label>用户名/邮箱(必填)</label>
                <input type="text" v-model="regForm.username" placeholder="请输入用户名/邮箱" @keypress.enter="reg()"/>
                <label>密码(必填)</label>
                <input type="password" v-model="regForm.password" placeholder="请输入密码" @keypress.enter="reg()"/>
                <label>确认密码(必填)</label>
                <input type="password" v-model="regForm.rePassword" placeholder="请再次输入密码" @keypress.enter="reg()"/>
                <label>邮箱(必填)</label>
                <input :class="disableEmail?'email disableEmail':'email'" :disabled="disableEmail" v-model="regForm.email" type="email" placeholder="请输入邮箱" @keypress.enter="login()"/>
                <div class="alerts" v-if="!hideHcaptcha">
                    完成人机验证以发送邮件
                </div>
                <label>邮箱验证码(必填)</label>
                <div class="code" v-if="!hideHcaptcha">
                    <vue-hcaptcha @verify="hcapResult" :sitekey=hCaptchaKey :size="hideHcaptcha?'invisible':'normal'" ></vue-hcaptcha>
                </div>
                <input type="email" v-model="regForm.emailCode" placeholder="请输入您收到的验证码" @keypress.enter="reg()"/>
                <qhButton :btnClass="'finishBtn'" text="完成注册" @click="reg()"/>
                <div class="btnGroup">
                    <div class="btn" @click="$root.goLogin()">去登录</div>
                    <div class="btn active">注册</div>
                </div>
            </div>
        </dialogLib>
    </div>
</template>

<script>
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';
import axios from 'axios';

import qhButton from "@/components/lib/unit/buttonLib.vue"
import dialogLib from '@/components/lib/unit/dialogLib.vue';


export default{
    name:"regBox",
    components:{
        VueHcaptcha,
        qhButton,
        dialogLib
    },
    watch:{
        "regForm.email":{
            handler(val){
                if(val==""){
                    this.disableEmail = false;
                    this.hideHcaptcha = true;
                }
                else{
                    if(val.indexOf("@")!=-1 && val.indexOf(".")!=-1){
                        this.hideHcaptcha = false;
                    }
                    else{
                        this.hideHcaptcha = true;
                    }
                }
            },
            immediate:true,
            deep:true
        },
    },
    data(){
        return{
            URL:window.config.apiUrl,
            hCaptchaKey : window.config.hCaptcha,
            regForm:{
                code:"",
                username:"",
                password:"",
                rePassword:"",
                email:"",
                emailCode:""
            },
            disableEmail:false,
            hideHcaptcha:true,
        }
    },
    methods:{
        //hcaptcha验证结果
        hcapResult(token){
            this.regForm.code = token;
            if(token){
                this.hideHcaptcha = true;
                this.sendMail();
            }
        },
        //发送邮箱验证码
        sendMail(){
            if(this.regForm.email==""){
                this.$notify.error({
                    title: '提示',
                    message:"请填写邮箱",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            if(!this.regForm.email.match(/^[a-zA-Z0-9_-]+@[a-zA-Z0-9_-]+(\.[a-zA-Z0-9_-]+)+$/)){
                this.$notify.error({
                    title: '提示',
                    message:"邮箱格式错误",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            if(this.regForm.code==""){
                this.$notify.error({
                    title: '提示',
                    message:"请先通过人机验证",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            this.$root.loading(true);
            let data = {
                email:this.regForm.email,
                id:"reg",
                code:this.regForm.code
            }

            axios.post(this.URL+"/user/sendMail",data)
            .then(res=>{ 
                let data = res.data ?? null;
                if(data.code == -1){
                    this.$notify.error({
                        title: '提示',
                        message:data.msg,
                        offset:this.$root.notifyOffet
                    });
                    this.$root.loading(false);
                    this.hideHcaptcha = false;
                    return;
                }
                this.disableEmail = true;
                this.$root.loading(false);
                this.$notify.success({
                    title: '提示',
                    message:data.msg,
                    offset:this.$root.notifyOffet
                });
            })
            .catch(err=>{ 
                this.hideHcaptcha = false;
                this.$root.loading(false);
                this.$emit('hide',true);
                this.$nextTick(()=>{
                    this.$root.netError(err);
                });
            })
        },
        reg(){
            //检查表单S
            if(this.regForm.username=="" || this.regForm.password=="" || this.regForm.rePassword=="" || this.regForm.email=="" || this.regForm.emailCode==""){
                this.$notify.error({
                    title: '提示',
                    message:"请填写完整信息",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            if(this.regForm.password != this.regForm.rePassword){
                this.$notify.error({
                    title: '提示',
                    message:"两次密码不一致",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            if(!(this.regForm.email.indexOf("@")!=-1 && this.regForm.email.indexOf(".")!=-1)){
                this.$notify.error({
                    title: '提示',
                    message:"邮箱格式错误",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            this.$root.loading(true);
            //检查表单E
            //修改数据
            this.regForm.code = this.regForm.emailCode;
            axios.post(this.URL+"/user/register",this.regForm)
            .then(res=>{ 
                let data = res.data ?? null;
                if(data.code == -1){
                    this.$notify.error({
                        message:data.msg,
                        offset:this.$root.notifyOffet
                    });
                    this.$root.loading(false);
                    return;
                }
                this.$root.loading(false);
                this.$nextTick(()=>{
                    this.$notify.success({
                        title: '提示',
                        message:"注册成功",
                        offset:this.$root.notifyOffet
                    });
                    this.$root.goLogin();
                });
            })
            .catch(err=>{ 
                this.$root.loading(false);
                this.$nextTick(()=>{
                    this.$root.netError(err);
                });
            })
        },
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/lib/regBox.scss"> 
</style>