<template>
    <div id="appealBox">
        <dialogLib
        ref="appeal"
        title='解封申诉'
        titleSize="2rem"
        @hide="$emit('hide',true)"
        width="450px"
        height="auto"
        >
            <div class="form">
                <div class="alerts">
                    <el-alert
                        title='我们很抱歉的通知您,你的账号因违反平台规则而被封禁.'
                        type="error"
                        :closable="false">
                    </el-alert>
                    <el-alert
                        :title="reason"
                        type="error"
                        :closable="false">
                    </el-alert>
                    <el-alert
                        title="您可以尝试填写以下表单材料,提交申诉进行解封,但我们不保证您的申诉一定会通过."
                        type="error"
                        :closable="false">
                    </el-alert>
                </div>
                <label>用户名/邮箱(必填)</label>
                <input type="text" :readonly="form.username!=''" v-model="form.username" placeholder="请输入用户名/邮箱"/>
                <label>申诉内容(必填)</label>
                <textarea rows="10" class="contnet" v-model="form.content"></textarea>
                <div class="alerts">
                    完成人机验证以提交申诉
                </div>
                <div class="code" v-if="!hideHcaptcha">
                    <vue-hcaptcha @verify="hcapResult" :sitekey=hCaptchaKey :size="hideHcaptcha?'invisible':'normal'" ></vue-hcaptcha>
                </div>
            </div>
        </dialogLib>
    </div>
</template>

<script> 
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';
import axios from 'axios';

import dialogLib from "@/components/lib/unit/dialogLib.vue";

export default{
    name:"appealBox",
    components:{
        VueHcaptcha,
        dialogLib,
    },
    props:{
        reason:{
            type:String,
            default:""
        },
        username:{
            type:String,
            default:""
        }
    },
    data() {
        return {
            form:{
                username:"",
                content:"",
                code:""
            },
            URL:window.config.apiUrl,
            hCaptchaKey : window.config.hCaptcha,
            hideHcaptcha:false,
        }
    },
    watch:{
    },
    methods:{
        //hcaptcha验证结果
        hcapResult(token){
            this.form.code = token;
            if(token){
                this.hideHcaptcha = true;
                this.submit();
            }
        },
        submit(){
            //检查表单S
            if(this.form.username=="" || this.form.content==""){
                this.$notify.error({
                    title: '提示',
                    message:"请填写完整信息",
                    offset:this.$root.notifyOffet
                });
                this.$nextTick(()=>{
                    this.hideHcaptcha = false;
                })
                this.form = {
                    username:"",
                    content:"",
                    code:""
                };
                return;
            }
            if(this.form.code==""){
                this.$notify.error({
                    title: '提示',
                    message:"请完成人机验证",
                    offset:this.$root.notifyOffet
                });
                return;
            }
            //检查表单E
            this.$root.loading(true);
            axios.post(this.URL+"/ticket/unban",this.form)
            .then(res=>{
                let data = res.data ?? null;
                if(data.code == -1){
                    this.$notify.error({
                        title: '提示',
                        message:data.msg,
                        offset:this.$root.notifyOffet
                    });
                    this.$root.loading(false);
                    this.$root.enableMask(false);
                    this.hideHcaptcha = false;
                    return;
                }
                if(data.code == -2){
                    this.$root.loading(false);
                    this.$root.enableMask(false);
                    this.hideHcaptcha = false;
                    this.$emit('appeal',true);
                    return;
                }
                this.$root.enableMask(false);
                this.$root.loading(false);
                this.$notify.success({
                    title: '提示',
                    message:"提交成功,请等待审核.",
                    offset:this.$root.notifyOffet
                });
                this.$refs?.appeal?.hide();                
                this.$emit('hide',true);
            })
            .catch(err=>{ 
                this.hideHcaptcha = false;
                this.$root.enableMask(false);
                this.$root.loading(false);
                this.$emit('hide',true);
                this.$nextTick(()=>{
                    this.$root.netError(err);
                });
            })
        },
    },
    created(){
        this.form.username = this.username;
    },
}

</script>

<style lang="scss" scoped src="@/assets/css/lib/appealBox.scss">

</style>