<template>
    <div id="announceList" class="card">
        <!-- 公告列表S -->
        <div class="cardTitle">公告列表</div>
        <div :class="loading?'content loadingLocal':'content'">
            <ul>
                <li class="item" v-for="(item,key) in normalData?.data" :key="'announce_'+key" @click="showDetail(item)">
                    <i class="icon el-icon-info"></i>{{ item.title }}
                </li>
                <li class="item more" v-if="showMore&&normalData?.data?.length>0" @click="more">查看更多<i class="icon el-icon-more"></i></li>
            </ul>
        </div>
        <!-- 公告列表E -->

        <!-- 全部公告S -->
        <dialogLib v-if="isShowMore" title="全部公告" @hide="closeMore" top="80px">
            <div :class="allAnnounceLoading?'content loadingLocal':'content'">
                <ul>
                    <li class="item" v-for="(item,key) in data?.data" :key="'announce_'+key" @click="showDetail(item)">
                        <i class="icon el-icon-info"></i>{{ item.title }}
                    </li>
                </ul>
                <div class="pagenation" v-if="data?.data?.length > 0">
                    <pageLib :total="data?.last_page" :page="data?.current_page" :limit="10" @change="changePage"/>
                </div>                
            </div>
        </dialogLib>
        <!-- 全部公告E -->

        <!-- 公告详情S -->
        <dialogLib v-if="title!=''" :title="'公告详情-'+title" @hide="close" top="80px" :hideWithMask="isShowMore?false:true">
            <div class="detail" v-html="content"></div>
        </dialogLib>
        <!-- 公告详情E -->
    </div>
</template>

<script>
import axios from 'axios';

import pageLib from '@/components/lib/unit/pageLib.vue';
import dialogLib from "@/components/lib/unit/dialogLib.vue";

export default {
    name:"announceList",
    components:{
        pageLib,
        dialogLib
    },
    props:{
      showMore:{
          type:Boolean,
          default:true
      }  
    },
    data(){
        return {
            URL:window.config.apiUrl,
            data:[],
            normalData:[],
            content:"",
            title:"",
            isShowMore:false,
            limit:10,
            moreLimit:20,
            loading:false,
            allAnnounceLoading:false,
            axiosC:new AbortController(),    //请求列表 用于取消请求
        }
    },
    methods:{
        //翻页
        async changePage(v){
            this.allAnnounceLoading = true;
            this.data = [];
            await this.getAnnounce(v,this.moreLimit);
            this.allAnnounceLoading = false;
        },
        //获取公告
        async getAnnounce(page=1,limit=10){
            let res = null;
            let token = this.$root.getLocalToken();
            if(!token){
                return;
            }
            try{
                res = await axios.get(this.URL+"content/announce?page="+page+"&limit="+limit,
                {
                    headers:{"token":token},
                    signal: this.axiosC.signal
                },
            );
            }
            catch(err){ 
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else{
                    this.loading= false;
                    this.$root.netError(err);
                }      
                return;
            }            
            this.loading= false;
            if(res?.data?.code == -1){
                this.$notify.error({
                    title: '提示',
                    message: res.msg,
                    offset:this.$root.notifyOffet
                });
                return;
            }
            if(this.normalData.length == 0){
                this.normalData = res?.data?.data;
            }
            this.data = res?.data?.data;
        },
        //公告详情
        showDetail(item){
            this.title = item.title;
            this.content = item.content;
        },
        //关闭详情
        close(){
            this.content = "";
            this.title = "";
        },
        //更多公告
        async more(){
            this.data = [];
            this.allAnnounceLoading = true;
            this.isShowMore = true;
            await this.getAnnounce(1,this.moreLimit);           
            this.allAnnounceLoading = false;
        },
        //关闭更多公告
        closeMore(){
            this.isShowMore = false;
            if(this.content != "" || this.title != ""){
                return;
            }
        }
    },
    mounted() { 
        this.$nextTick(() => { 
            this.loading= true;
            this.getAnnounce(1,this.limit);
        });
    },
    beforeDestroy(){
        this.axiosC.abort();
    }
}
</script>

<style lang="scss" scoped src="@/assets/css/lib/announceList.scss">

</style>