<template>
  <div id="app">
    <menuTop :userInfo="userInfo" v-if="$route.path!='/'"/>
    <div :class="$route.path!='/'?'routeView body':'routeView'">
      <router-view
      :style="$route.path=='/'?'':'margin-top:calc(60px + 1px + 40px);margin-bottom: 40px;'"
      /> <!-- 路由视图 -->
    </div>  
    <footerBox v-if="$route.path!='/'"/>  

    <!-- 遮罩S -->
    <div ref="mask" class="mask" :style="'top:' +maskOffet+';'" v-show="mask"></div>
    <!-- 遮罩E -->

    <!-- 登录 -->
    <loginBox v-if="isShowLogin" @hide="closeLogin" @appeal="openAppeal"/>

    <!-- 注册 -->
    <regBox v-if="isShowReg" @hide="isShowReg=false"/>

    <!-- 申诉 -->
    <appealBox v-if="isShowAppealBox" @hide="closeAppeal" :reason="banReason" :username="banUsername"/>

    <div class="netError" v-if="isNetERR">
      <div class="title">网络错误</div>
      <div class="text">鹤儿迷路了，正在寻找信号…</div>
      <div class="btnGroup">
        <div class="btn" @click="refresh()">
          <img :src="logo"/>重新连接
        </div>
      </div>
    </div>

    <div class="loadingBg" v-if="loading">
      <div class="loading">
        <div class="imgBox">
          <img class="imgOuter loading-animation" :src="loadingIcon"/>
          <div class="text">加载中</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import logo from "@/assets/img/logo.png"
import loadingIcon from "@/assets/img/loading.svg"

import regBox from "@/components/lib/regBox.vue";
import loginBox from "@/components/lib/loginBox.vue";
import appealBox from "@/components/lib/appealBox.vue";
import menuTop from "@/components/lib/menuTop.vue";
import footerBox from "@/components/lib/footerBox.vue";

import axios from "axios";

export default {
  name: 'App',
  components: {
    regBox,
    loginBox,
    menuTop,
    footerBox,
    appealBox
  }, 
  data() {
    return {
      URL:window.config.apiUrl,
      isNetERR:false,
      isShowLogin:false,
      isShowReg:false,
      isShowAppealBox:false,
      logo:logo,
      loadingIcon:loadingIcon,
      scrollLock:false,
      mask:false,
      maskOffet:0,
      loading:false,
      userInfo: null,
      maskTimer:[],
      banReason:"",
      banUsername:"",
    }
  },
  watch: {
    loading: {
      handler(val) {
        if (val) {
          this.mask = true;
          this.scrollLock = true;
        }
        else if(!this.loading && !this.isShowLogin && !this.isShowReg && !this.isShowAppealBox){
          let haveDialog = document.querySelector(".dialog") != null;
          if(!haveDialog){
            this.mask = false;
          }          
          this.scrollLock = false;
        }
      },
      immediate: true
    },
    "isShowLogin":function(val){
      if(val || this.isShowReg){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else if(!this.loading && !val && !this.isShowReg){
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "isShowReg":function(val){
      if(val || this.isShowLogin){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else if(!this.loading && !val && !this.isShowLogin){
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "isShowAppealBox":function(val){
      if(val || this.isShowReg){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else if(!this.loading && !val && !this.isShowReg){
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "isNetERR":function(val){
      if(val){
        this.mask = true;
        this.$root.scrollLock = true;
      }
      else{
        this.mask = false;
        this.$root.scrollLock = false;
      }
    },
    "$route.path":function(val){
      if(val != "/"){
        this.LocalGetUserInfo();
      }
    }
  },
  methods: {
    //刷新页面
    refresh(){
      window.location.reload();
    },
    //网络错误
    netError(err=null){
      let status = err?.status ?? null;
      if(!status || status != 401){
        console.log(err);
        this.isNetERR = true;
        this.enableMask(true);
      }
    },
    //前往登录页
    goLogin(){      
      this.isShowReg = false; 
      this.isShowLogin = true; 
    },
    //前往注册页
    goReg(){      
      this.isShowLogin = false; 
      this.isShowReg = true; 
    },
    
    /**
     * 启用/禁用遮罩
     * @param isShow true:启用  false:禁用
     * @param offset 遮罩顶部偏移量 可传任意字符串 如 0 60px 20% 50vh
     */
    enableMask(isShow=true,offset=0){ 
      try{
        let timeout = 5;
        if(isShow){
            this.maskOffet = offset;
            document.documentElement.style.overflow = "hidden";
            this.mask = true;
        }
        if(!isShow){
          document.documentElement.style.overflow = "auto";
          this.$refs.mask.classList.remove("active");
          timeout = 300;
        }
        let timer = setTimeout(() => {
          if(isShow){
            this.$refs.mask.classList.add("active");  
          }
          else{
            this.mask = false;
            this.maskOffet = 0;        
          }
          //清理所有计时器
          for(let i = 0; i < this.maskTimer.length; i++){
            clearTimeout(this.maskTimer[i]);
          }
        }, timeout); 
        this.maskTimer.push(timer);
      }
      catch(err){
        console.log("遮罩可能已关闭");
      }
    },
    //获取用户信息
    async getUserInfo(){
      let token = localStorage.getItem("token") ?? null;
      if(!token){
        return null;
      }
      //将token加入header
      let header = {headers:{token:token}};
      let data = null;
      try{
        data = await axios.get(this.URL+"/user/index",header);
      }
      catch(err){
        this.netError(err);
        return null;
      }
      return data.data ?? null;
    },
    //退出登录
    async logout(){
      localStorage.removeItem("token");
      window.location.reload();
    },
    //判断登录状态
    async checkLogin(){ 
      let hasUserInfo = await this.getUserInfo();
      if(!localStorage.getItem("token") || !hasUserInfo){
        return false;
      }
      return hasUserInfo;
    },
    //全屏加载
    loadingEvent(isLoading=true){
      this.loading = isLoading;
    },
    //获取前端存储的TOKEN
    getLocalToken(){
      let token = localStorage.getItem("token") ?? null;
      if(!token){
          if(this.$route.path != "/"){
              this.$router.replace("/");
          }
          this.$root.goLogin();
          return null;
      }
      return token;
    },
    //字符串域名数据转换为数组
    formatterDomains(str){
      let arr = JSON.parse(str);
      return arr
    },
    //页面跳转
    linkTo(url,target="_self"){
      if(this.$route.path != url && target == "_self"){
        this.$router.replace(url);
      }
      if(target == "_blank"){
        window.open(url);
      }
    },
    //时间戳转换为日期
    getDate(time) {
        let date = new Date(time*1000);
        let year = date.getFullYear();
        let month = date.getMonth() + 1;
        let day = date.getDate();
        let hour = date.getHours();
        let minute = date.getMinutes();
        let second = date.getSeconds();
        //补0
        month = month < 10 ? '0' + month : month;
        day = day < 10 ? '0' + day : day;
        hour = hour < 10 ? '0' + hour : hour;
        minute = minute < 10 ? '0' + minute : minute;
        second = second < 10 ? '0' + second : second;
        return year + '-' + month + '-' + day + ' ' + hour + ':' + minute + ':' + second;
    },
    //取文本摘要
    shortStr(str,len=10){
      if(str.length <= len){
        return str;
      }
      return str.substring(0,len)+"...";
    },
    // 移动端检测
    checkMob() {
      let ua = navigator.userAgent || navigator.vendor || window.opera;
      return /Android|iPhone|iPad|iPod|Windows Phone|webOS|BlackBerry|IEMobile|Opera Mini|Mobi/i.test(ua);
    },
    //自调用S
    //获取用户信息
    async LocalGetUserInfo() {
      let res = await this.checkLogin();
      if(!res){
        if(this.$route.path != "/"){
          this.$router.replace("/");
          this.goLogin();
        }     
      }
      this.userInfo = res?.data ?? null;
    },
    //关闭登录窗口
    closeLogin(){
      this.isShowLogin=false;
    },
    //打开申诉窗口
    openAppeal(reason="",usernmae=""){
      this.banReason = reason;
      this.banUsername = usernmae;
      this.isShowAppealBox = true;
      this.closeLogin();
    },
    //关闭申诉窗口
    closeAppeal(){
      this.isShowAppealBox = false;
    },
    //自调用E
  },
  created(){
    // 移动端跳转
    if(this.checkMob() && window.config.mobPage && !window.config.DEBUG){
      window.location.href = window.config.mobPage;
    }
    this.LocalGetUserInfo();
  },
  mounted(){
      //注册全局方法S
      this.$root.notifyOffet = 61;                            //通知偏移
      this.$root.scrollLock = this.scrollLock;                //锁定首页滚动
      this.$root.netError = this.netError;                    //网络错误
      this.$root.goLogin = this.goLogin;                      //跳转登录
      this.$root.goReg = this.goReg;                          //跳转注册
      this.$root.enableMask = this.enableMask;                //显示遮罩
      //this.$root.getUserInfo = this.getUserInfo;            //获取用户信息(暂不提供调用 请使用checkLogin方法获取用户信息)
      this.$root.logout = this.logout;                        //登出
      this.$root.checkLogin = this.checkLogin;                //检查登录状态
      this.$root.loading = this.loadingEvent;                 //全屏加载提示
      this.$root.getLocalToken = this.getLocalToken;          //获取前端存储的token
      this.$root.formatterDomains = this.formatterDomains;    //将字符串域名数据转换为数组
      this.$root.linkTo = this.linkTo;                        //页面跳转
      this.$root.getDate = this.getDate;                      //时间戳转换为日期
      this.$root.shortStr = this.shortStr;                    //取文本摘要
      this.$root.checkMob = this.checkMob;                    //检测当前设备是否为移动端
      //注册全局方法E
  }
}
</script>
<style lang="scss" src="@/assets/css/app.scss"></style>