<template>
    <div id="ticketPage" class="page">
        <div class="card">
            <div class="cardTitle">工单列表</div>
            <div class="toolsBar">
                <div class="row">
                    <div class="btnGroup">
                        <buttonLib btnClass="btn" text="发起工单" @click="newTicket"/>
                        <buttonLib btnClass="btn" text="刷新" @click="getData(limit,1)"/>
                    </div>                    
                </div>
            </div>

            <div :class="loading?'tableBox loadingLocal':'tableBox'">
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column prop="no" label="工单编号" :resizable="false">
                    </el-table-column>

                    <el-table-column prop="title" label="最新内容" :resizable="false">
                        <template slot-scope="scope">
                            {{$root.shortStr(scope.row.title)}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="status" label="回复状态" :resizable="false">
                        <template slot-scope="scope">
                            <el-tag v-if="scope.row.status===0" type="info">等待回复</el-tag>
                            <el-tag v-if="scope.row.status===1" type="success">已回复</el-tag>
                            <el-tag v-if="scope.row.status===2" type="danger">已关闭</el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column prop="title" label="创建时间" :resizable="false">
                        <template slot-scope="scope">
                            {{$root.getDate(scope.row.createtime)}}
                        </template>
                    </el-table-column>

                    <el-table-column prop="title" label="更新时间" :resizable="false">
                        <template slot-scope="scope">
                            {{$root.getDate(scope.row.updatetime)}}
                        </template>
                    </el-table-column>

                    <el-table-column fixed="right" label="操作" width="200" :resizable="false">
                        <template slot-scope="scope">
                            <div class="btnGroup">
                                <buttonLib @click="showTicket(scope.row)" btnClass="rowBtn" size="small" text="查看" />
                                <buttonLib v-if="scope.row.status===0||scope.row.status===1" @click="closeTicket(scope.row)" btnClass="rowBtn" size="small" text="关闭" />
                                <buttonLib v-if="scope.row.status===2" @click="delTicket(scope.row)" btnClass="rowBtn" size="small" text="删除" />
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagenation">
                    <pageLib :total="totalPage" :page="currentPage" @change="changePage"/>
                </div>
            </div>

            <!-- 工单详情S -->
            <dialogLib ref="detail" v-if="showTicketContent" title="工单详情" @hide="closeDetail" height="auto" max-height="80vh" max-width="1000px" >
                <div ref="detailContent" :class="dialogLoading?'detail loadingLocal':'detail'">
                    <div class="title">工单编号:{{ detailData[0]?.no }}</div>
                    <div v-for="(row,key) in detailData" :key="'detail_'+key" :class="row.role=='user'?'row right':'row'">
                        <div class="role">
                            {{ row.role=='user'?'我':'客服人员' }} {{ $root.getDate(row.createtime) }}
                        </div>
                        <div class="msg">
                            {{ row.content }}
                        </div>
                    </div>
                </div>
                <div class="inputBox">
                    <textarea rows="6" :class="disabled?'disabled':''" :disabled="disabled" v-model="reForm.content" placeholder="请输入内容"></textarea>
                    <div :class="disabled?'send disabled':'send'" @click="replyBtn">
                        发送
                    </div>
                </div>
            </dialogLib>
             <!-- 工单详情E -->

             <!-- hcaptcha 验证S -->
            <dialogLib v-if="isShowHcaptcha" title="人机验证" :hideWithMask="false" @hide="closeHcaptcha" height="auto" max-width="400px" >
                <vue-hcaptcha @verify="hcapResult" :sitekey="hCaptchaKey" ></vue-hcaptcha>
            </dialogLib>
            <!-- hcaptcha 验证E -->

            <!-- 二次确认S -->
             <dialogLib ref="confirm" v-if="isShowConfirm" title="二次确认" @hide="hideConfirm" :isShowClose="false" height="auto" max-width="400px" >
                <div class="confirm">
                    <div class="row">
                        <div class="title">{{ confirmTitle }}</div>
                    </div>
                    <div class="row">
                        <div class="content" v-html="confirmContent"></div>
                    </div>
                    <div class="row">
                        <buttonLib btnClass="btn yes" text="确定" @click="confirmYes"/>
                        <buttonLib btnClass="btn no" text="取消" @click="confirmNo"/>
                    </div>
                </div>
             </dialogLib>
             <!-- 二次确认E -->

             <!-- 新工单S -->
            <dialogLib ref="newTicket" v-if="isShowNewTicket" title="发起工单" @hide="hideNewTicket" height="auto" max-height="80vh" max-width="400px" >
                <div class="newTicket">
                    <div class="row">
                        <el-alert
                            title="工单系统仅用于真实问题反馈，请勿随意提交无关信息。若出现恶意或过度滥用，您的账号可能会被限制使用。"
                            type="error"
                            effect="dark"
                            :closable="false">
                        </el-alert>
                    </div>
                    <div class="row"> 
                        <textarea rows="10" v-model="newTicketFrom.content" placeholder="请输入内容"></textarea>
                    </div>
                    <div class="row">
                        <buttonLib btnClass="btn" text="提交" @click="newTicketBtn"/>
                    </div>
                </div>
             </dialogLib>
             <!-- 新工单E -->
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';

import buttonLib from '@/components/lib/unit/buttonLib.vue';
import pageLib from '@/components/lib/unit/pageLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';

export default {
    name: 'ticketPage',
    components: {
        buttonLib,
        pageLib,
        dialogLib,
        VueHcaptcha
    },
    data() {
        return {
            loading:false,
            dialogLoading:false,
            URL: window.config.apiUrl,
            axiosC: new AbortController(),    //请求列表 用于取消请求
            tableData:[],
            totalPage:1,
            currentPage:1,
            limit:10,
            detailData:[],
            showTicketContent:false,
            disabled:false,
            isShowConfirm:false,
            confirmTitle:"",
            confirmContent:"",
            confirmAction:function(){
                console.log("二次确认");
            },

            reForm:{
                code:"",
                no:"",
                content:""
            },
            hCaptchaKey : window.config.hCaptcha,
            hCaptchaType:'newTicket',

            isShowHcaptcha:false,
            isShowNewTicket:false,
            newTicketFrom:{
                code:"",
                content:""
            }
        }
    },
    methods: {
        //将工单提交至后端
        async submitTicket(){ 
            let data = null;
            this.$root.loading(true);
            try {
                data = await axios.post(
                    this.URL + "ticket/submit",
                    this.newTicketFrom,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            this.$refs.newTicket?.hide();
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: "提示",
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: "提示",
                message: "提交成功",
            });
            this.getData(this.limit,1);
        },
        //新工单提交按钮
        newTicketBtn(){
            if(this.newTicketFrom?.content == ""){
                this.$notify.error({
                    title: "提示",
                    message: "请填写工单内容",
                });
                return;
            }
            this.hCaptchaType = "new";
            this.isShowHcaptcha = true;
        },
        //发起工单
        newTicket(){
            this.isShowNewTicket = true;
        },
        //关闭新工单窗口
        hideNewTicket(){
            this.isShowNewTicket = false;
            this.newTicketFrom.content = "";
        },
        //关闭二次确认窗口
        hideConfirm(){
            this.isShowConfirm = false;
        },
        //确认二次确认窗口
        confirmYes(){
            this.confirmAction();
            this.$refs.confirm?.hide();
        },
        //取消二次确认窗口
        confirmNo(){
            this.$refs.confirm?.hide();
        },
        //回复按钮
        replyBtn(){
            if(this.reForm?.content == ""){
                this.$notify.error({
                    title: "提示",
                    message: "请填写回复内容",
                });
                return;
            }
            this.hCaptchaType = "reply";
            this.isShowHcaptcha = true;
        },
        //关闭hcaptcha
        closeHcaptcha(){
            this.isShowHcaptcha = false;
        },
        //hcaptcha验证结果
        async hcapResult(token){
            if(this.hCaptchaType == 'reply'){
                this.reForm.code = token;
                this.reForm.no = this.detailData[0]?.no;
            }
            if(this.hCaptchaType == 'new'){
                this.newTicketFrom.code = token;
            }
            if(token){
                this.closeHcaptcha();
                if(this.hCaptchaType == 'reply'){
                    await this.sendReply();
                    this.reForm = {
                        code:"",
                        no:"",
                        content:""
                    };
                    this.$refs.detail?.hide();
                }
                if(this.hCaptchaType == 'new'){
                    await this.submitTicket();
                    this.newTicketFrom = {
                        code:"",
                        content:""
                    };
                    this.$refs.newTicket?.hide();
                }                
                this.getData(this.limit,1); 
            }
        },
        //发送回复
        async sendReply(){ 
            let data = null;
            this.$root.loading(true);
            try {
                data = await axios.post(
                    this.URL + "ticket/reply",
                    this.reForm,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }
           
            this.$notify.error({
                title: '提示',
                message: data?.data?.msg,
            });
        },
        //关闭工单
        closeTicket(row){
            //重写二次确认动作
            this.confirmAction = function(){
                this.doTicketAction(row,"close");
            }
            this.confirmTitle="确定要关闭工单吗？"
            this.confirmContent="您即将关闭以下工单:<br><span style='color:red;'>"+row?.no+"</span><br>关闭后,该工单将无法再进行回复";
            this.isShowConfirm = true;
        },
        //删除工单
        delTicket(row){
            //重写二次确认动作
            this.confirmAction = function(){
                this.doTicketAction(row,"del");
            }
            this.confirmTitle="确定要删除工单吗？"
            this.confirmContent="您即将删除以下工单:<br><span style='color:red;'>"+row?.no+"</span><br>删除后,该工单将不会出现在工单列表.同时无法找回";
            this.isShowConfirm = true;
        },
        //工单操作请求
        async doTicketAction(row,action="close"){ 
            let data = null;
            let postData = {
                no: row?.no,
                action: action,
            };
            this.$root.loading(true);
            try {
                data = await axios.post(
                    this.URL + "ticket/close",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            if(data?.data?.code!=1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: '提示',
                message: data?.data?.msg,
            });
            this.getData(this.limit,1);
        },
        //关闭工单详情
        closeDetail(){
            this.showTicketContent = false;
            this.detailData = [];
        },
        //打开工单详情
        showTicket(row){
            this.showTicketContent = true;
            this.disabled = row?.status == 2;
            this.getDetail(row.no);
        },
        changePage(page){
            this.getData(this.limit,page);
        },
        //获取工单详情
        async getDetail(no){
            let data = null;
            this.dialogLoading = true;
            try {
                data = await axios.get(
                    this.URL + "ticket/view?no="+no,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.dialogLoading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.dialogLoading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }
            this.detailData = data?.data?.data;
            //滚动到底部
            if(this.$refs.detailContent){
                this.$nextTick(() => {
                    //平滑滚动
                    this.$refs.detailContent.scrollTo({
                        top: this.$refs.detailContent.scrollHeight,
                        behavior: 'smooth'
                    });
                });
            }
        },
        //获取数据
        async getData(limit=10,page=1) {
            this.loading = true;
            this.tableData = [];
            this.currentPage = page;

            let data = null;
            try {
                data = await axios.get(
                    this.URL + "ticket/list?page="+page+"&limit="+limit,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            let result = data?.data?.data;
            this.totalPage = result?.last_page;
            this.tableData = result?.data;
        },

    },
    mounted() {
        this.$nextTick(() => { 
            this.getData(this.limit,1);
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/ticketPage.scss">

</style>