<template>
    <div class="serverStatus page">
        <div class="card">
            <div class="cardTitle">服务器状态</div>
            <div class="toolsBar">
                <div class="row">
                    <div class="title">搜索</div>
                </div>
                <div class="row">
                    <el-select v-model="groupFilter" collapse-tags placeholder="请选择服务器分组">
                        <el-option label="全部" value="all">
                        </el-option>
                        <el-option v-for="item in gourps" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                    <el-select v-model="protocolFilter" multiple collapse-tags placeholder="请选择协议">
                        <el-option v-for="item in protocols" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                </div>
                <div class="row">
                    <el-alert
                        title="服务器负载展示过去5分钟内的服务器平均负载"
                        type="info"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title="若服务器状态为离线 则表示该服务器可能正在维护或遭受攻击"
                        type="info"
                        :closable="false">
                    </el-alert>
                </div>
            </div>

            <div :class="loading?'tableBox loadingLocal':'tableBox'">
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column prop="name" label="服务器名称" :resizable="false"></el-table-column>

                    <el-table-column prop="group_arr" label="可用用户组" :resizable="false">
                        <template slot-scope="scope">
                            <div class="tagGroup">
                                <el-tag v-for="item in scope.row.group_arr" :key="item" :disable-transitions="true" type="info" size="small">{{item}}</el-tag>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column prop="protocol" label="支持的协议" :resizable="false">
                        <template slot-scope="scope">
                            <div class="tagGroup">
                                <el-tag v-for="item in scope.row.protocol" :key="item" :disable-transitions="true"  type="info" size="small">{{item}}</el-tag>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column prop="normal" label="状态" :resizable="false">
                        <template slot-scope="scope">
                            <div class="tagGroup centerData">
                                <el-tag v-if="scope.row.checkStatus" :disable-transitions="true"  type="success">在线</el-tag>
                                <el-tag v-if="!scope.row.checkStatus" :disable-transitions="true"  type="info">离线</el-tag>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column prop="payload_time" label="运行时间" align="center" :resizable="false"></el-table-column>
                    <el-table-column prop="payload_load_5" label="负载" :resizable="false">
                        <template slot-scope="scope">
                            <div class="centerData">
                                <el-progress
                                :text-inside="false"
                                type="circle"
                                :width="100"
                                :percentage="parseFloat(scope.row.payload_load_5)"
                                :color="payloadColor"
                                :define-back-color="scope.row?.payloadBackColor ?? ''">
                                </el-progress>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column prop="updatetime" label="最后更新时间" :resizable="false">
                        <template slot-scope="scope">
                            <div class="centerData">
                                {{$root.getDate(scope.row.updatetime)}}
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagenation">
                    <pageLib :total="totalPage" :page="currentPage" @change="changePage"/>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import pageLib from '@/components/lib/unit/pageLib.vue';
import axios from 'axios';

export default {
    name: 'serverStatus',
    components: {
        pageLib,
    },
    data() {
        return {
            URL:window.config.apiUrl,
            loading:false,
            payloadColor:[
                {color: '#5cb87a', percentage: 40},
                {color: '#e6a23c', percentage: 70},
                {color: '#f56c6c', percentage: 100}
            ],
            currentPage:1,
            totalPage:1,      
            limit: 10,      
            tableData:[],
            axiosC: new AbortController(),    //请求列表 用于取消请求
            //筛选S
            gourps:[],
            groupFilter: "",
            protocols: [],
            protocolFilter: [],
            //筛选E
        }
    },
    computed:{

    },
    watch:{
        groupFilter:"debouncedGetServersData",
        protocolFilter:"debouncedGetServersData"
    },
    methods:{
        changePage(page){
            this.getServerData(page,this.limit);
        },
        //获取服务器数据
        async getServerData(page=1,limit=10){
            this.loading = true;
            let data = null;
            this.currentPage = page;
            //过滤器S
            let protocolsFilterText = "";
            let arr = [];
            if(this.protocolFilter.length == 0){                
                this.protocols.forEach(element => {
                    arr.push(element.value);
                });
            }
            else{
                this.protocolFilter.forEach(element => {
                    arr.push(element);
                });
            }
            protocolsFilterText = arr.join(",");

            let groupFilterText = this.groupFilter;
            //过滤器E
            let postData = {};
            if(groupFilterText!=''&&groupFilterText!='all'){
                postData = {
                    protocol:protocolsFilterText,
                    group:groupFilterText,
                };
            }
            else{
                postData = {
                    protocol:protocolsFilterText,
                };
            }
            postData.page = page;
            postData.limit = limit;
            try{
                data = await axios.post(this.URL+"frp/serverList",
                    postData,
                    {
                        headers:{token:localStorage.getItem("token")},
                        signal:this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.loading = false;
                this.$root.netError(err);
            }
            this.loading = false;
            this.tableData = data?.data?.data?.data;
            this.tableData.forEach(item => {
                if(item?.payload_load_5 < 40){
                    item.payloadBackColor = "#5cb87a33";
                }
                if(item?.payload_load_5 < 70 && item?.payload_load_5 >= 40){
                    item.payloadBackColor = "#e6a23c33";
                }
                if(item?.payload_load_5 > 70 ){
                    item.payloadBackColor = "#f56c6c33";
                }
                //前端检查时间对比最后更新时间以确定服务器状态
                let now = new Date().getTime();
                let updatetime = item.updatetime * 1000;
                //检查是否超过11分钟未更新
                if(now - updatetime > 11 * 60 * 1000){
                    item.checkStatus = false;
                }
                if(now - updatetime < 11 * 60 * 1000){
                    item.checkStatus = true;
                }
            });
            let selection = data?.data?.data?.selection;
            this.gourps = selection?.goups;
            this.protocols = selection?.protocols;
            this.totalPage = data?.data?.data?.last_page;
        },
        //获取数据
        async getData(){
            let data = null;
            this.loading = true;
            try{
                let postData = {
                    protocol:"",
                };
                data = await axios.post(this.URL+"frp/serverList",
                    postData,
                    {
                        headers:{token:localStorage.getItem("token")},
                        signal:this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.netError(err);
            }
            let selection = data?.data?.data?.selection;
            this.protocols = selection?.protocols;
            this.getServerData(1,this.limit);
        },
        //服务器搜索防抖
        debouncedGetServersData: (function () {
            let timer = null;
            return function () {
                if (timer) clearTimeout(timer);
                timer = setTimeout(() => {
                    this.getServerData();
                }, 500);
            }
        })()
    },
    mounted() {
        this.$nextTick(() => { 
            this.getData();
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}

</script>

<style scoped lang="scss" src="@/assets/css/view/serverStatusPage.scss"></style>