<template>
    <div id="menuTop" @mouseleave="hideChildList()">
        <div class="menuTop">
            <div class="logo" @click="$root.linkTo('/')">
                <img :src="logoPng"/>
                <img class="text" :src="fontPng"/>
            </div>
            <div class="menus">
                <ul class="main">
                    <li v-for="(item,key) in menus" :key="'mainMenu_'+key">
                        <a :class="item.isActive?'active':''" @click="linkTo(item)" @mouseenter="item?.isParent?showChildList(item):hideChildList()">
                            <i :class="'icon '+item?.icon"></i>{{ item?.name }}
                        </a>
                        {{ menus.length -1 == key ? '' : '|' }}
                    </li>
                </ul>
            </div>
            <div class="userInfo">
                <div class="name">{{ userInfo?.username ?? '未知用户' }}</div>
                <el-tooltip class="exitButton" effect="dark" content="退出" placement="bottom">
                    <div class="exit" @click="$root.logout()"></div>
                </el-tooltip>
            </div>
        </div>

        <div :class="childContent.length>0?'childBox active':'childBox'" :style="'height:'+computeMenuHeight+'px;'+(childContent.length>0?'':'border-bottom:0;padding:0;')">
            <div class="content widthLimit" :style="(childContent.length>0?'':'padding:0;')+(contentOverFlow?'overflow-y:auto;':'')">
                <div class="item" v-for="(child,key) in childContent" :key="'menuChild_'+key" @click="linkTo(child)">
                    <div class="title">
                        <i :class="'icon '+child?.icon"></i>{{ child?.name }}
                    </div>                    
                    <div class="content">{{ child?.content }}</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import fontPng from "@/assets/img/font.png";
import logoPng from "@/assets/img/logo.png";

export default {
    name: 'menuTop',
    props: {
        userInfo:{
            type:Object,
            default:function(){
                return null
            }
        }
    },
    data() {
        return {
            fontPng:fontPng,
            logoPng:logoPng,
            childContent:[],
            contentOverFlow:false,
            menus:[
                {
                    "name":"首页",
                    "url":"/usercenter",
                    "isParent":false,
                    "icon":"el-icon-s-home",
                    "isActive":false,
                },
                {
                    "name":"服务",
                    "isParent":true,
                    "icon":"el-icon-s-help",
                    "isActive":false,
                    "child":[
                        {
                            "name":"服务器状态",
                            "url":"/servers",
                            "icon":"el-icon-s-unfold",
                            "content":"在这里您可以查看所有服务器的状态与负载情况"
                        },
                        {
                            "name":"隧道列表",
                            "url":"/tunnel",
                            "icon":"el-icon-s-unfold",
                            "content":"在这里您可以添加、修改、删除、查看您所拥有的隧道列表"
                        },
                        {
                            "name":"软件下载",
                            "url":"/download",
                            "icon":"el-icon-download",
                            "content":"在这里您可以下载软件，并查看软件更新日志"
                        },
                        {
                            "name":"备案审核",
                            "url":"/domainauth",
                            "icon":"el-icon-s-claim",
                            "content":"在这里您可以对您的域名申请备案"
                        },
                        {
                            "name":"工单系统",
                            "url":"/ticket",
                            "icon":"el-icon-s-order",
                            "content":"在这里您可以向客服人员提交工单、查看或回复工单"
                        }
                    ]
                },
                {
                    "name":"用户",
                    "url":"/user",
                    "icon":"el-icon-user-solid",
                    "isParent":false,
                    "isActive":false,
                },
                {
                    "name":"商城",
                    "url":"/mall",
                    "icon":"el-icon-s-shop",
                    "isParent":false,
                    "isActive":false,
                },
                {
                    "name":"帮助",
                    "url":"/help",
                    "icon":"el-icon-question",
                    "isParent":false,
                    "isActive":false,
                },
            ],
        }
    },
    computed: { 
        computeMenuHeight(){
            return this.menuHeight();
        },
    },
    watch: { 
        //深度监听
        $route: {
            handler() {
                this.menuActive(this.$route.path);
            },
            deep: true,
            immediate: true
        },
    },
    methods: {
        //计算菜单激活项
        menuActive(url=""){ 
            if(this.menus.length === 0){
                return;
            }
            this.menus.forEach(mainItem => {
                // 默认不激活
                mainItem.isActive = false;

                // 直接匹配主菜单
                if(mainItem.url === url){
                    mainItem.isActive = true;
                    return;
                }

                // 匹配子菜单
                if(mainItem.child){
                    mainItem.isActive = mainItem.child.some(childItem => childItem.url === url);
                }
            });
        },

        // 计算菜单高度
        menuHeight() {
            const length = this.childContent.length;
            const rowHeight = 150;
            const colNum = 4;
            if (length === 0) return 0;
            const rows = Math.ceil(length / colNum);
            let result = rows * rowHeight + ((rows*20)+(rows*2)) + 20;
            //获取视口高度
            const vpHeight = window.innerHeight;
            if(vpHeight < result + 61){
                result = vpHeight - 61 - 200;
                this.contentOverFlow = true;
            }
            else{
                this.contentOverFlow = false;
            }
            return result;
        },
        showChildList(item){
            if(item?.isParent){
                this.childContent = item.child;
                this.$root.enableMask(true,"61px");
            }
        },
        hideChildList(){
            this.childContent = [];
            this.$root.enableMask(false);
        },
        linkTo(item){
            if(!item?.url||item?.isParent)return;
            let url = item?.url;
            this.hideChildList();
            this.$root.linkTo(url)
        }
    },
    mounted() {
        
    }
}
</script>

<style lang="scss" scoped src="@/assets/css/lib/menuTop.scss"></style>