<template>
    <div class="useCard card">
        <div class="cardTitle">
            <div>卡密激活</div>
            <div class="btn" @click="$root.linkTo('/#/mall','_blank')">
                <i class="icon el-icon-s-shop"></i>
                前去购买
            </div>
        </div>
        <div class="useInputBox">
            <input type="text" placeholder="请输入卡密" v-model="form.cardCode" class="cardInput">
            <buttonLib text="激活" @click="showHcaptcha"/>
        </div>        

        <!-- 折算提醒S -->
        <dialogLib ref="confirm" v-if="alertContent!=''" title="折算提醒" height="auto" max-width="400px" @hide="closeConfirm(true)" :isShowClose="false">
            <div class="confirm" v-html="alertContent">
            </div>
            <div class="help" @click="$root.linkTo('/#/help','_blank')">
                点击查看详细折算规则
            </div>
            <div class="confirmBtn">
                <buttonLib text="我已知晓折算规则-确认激活" class="btn yes" @click="showHcaptcha(true)"></buttonLib>
                <buttonLib text="取消" class="btn no" @click="closeConfirm"></buttonLib>                
            </div>
        </dialogLib>
        <!-- 折算提醒E -->

        <!-- hcaptcha 验证S -->
        <dialogLib ref="hcaptcha" v-if="isShowHcaptcha" title="人机验证" @hide="closeHcaptcha" height="auto" max-width="400px" >
            <vue-hcaptcha @verify="hcapResult" :sitekey="hCaptchaKey" ></vue-hcaptcha>
        </dialogLib>
        <!-- hcaptcha 验证E -->
    </div>
</template>

<script>
import axios from 'axios';
import buttonLib from '@/components/lib/unit/buttonLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';
import VueHcaptcha from '@hcaptcha/vue-hcaptcha';

export default {
    name: 'useCard',
    components:{
        buttonLib,
        dialogLib,
        VueHcaptcha
    },
    data:function(){
        return {
            URL:window.config.apiUrl,
            axiosC: new AbortController(),    //请求列表 用于取消请求
            alertContent:"",
            isShowHcaptcha:false,
            form:{
                code:"",
                cardCode:"",
            },
            isConfirm:false,
            hCaptchaKey : window.config.hCaptcha,
        }
    },
    methods:{
        //关闭hcaptcha
        closeHcaptcha(){
            this.isShowHcaptcha = false;
        },
        //hcaptcha验证结果
        async hcapResult(token){
            this.form.code = token;
            if(token){
                this.$refs?.hcaptcha?.hide();
                await this.useCard(this.isConfirm,token);
            }
        },
        //显示hcaptcha验证窗
        async showHcaptcha(isConfirm=false){ 
            if(this.form.cardCode==""){
                this.$notify.error({
                    title: "提示",
                    message: "请输入卡密",
                });
                return;
            }
            this.isConfirm = isConfirm;
            this.isShowHcaptcha = true;
        },
        closeConfirm(isExcuted=false){
            this.$refs?.confirm?.hide();
            if(isExcuted){
                this.alertContent = "";
            }            
        },
        async useCard(isConfirm=false,token=""){
            let data = null;
            this.$root.loading(true);
            try{
                let postData = null;
                if(isConfirm){
                    postData = {
                        code:token,
                        confirm:isConfirm,
                        cardCode:this.form.cardCode
                    };
                }
                else{
                    postData = {
                        cardCode:this.form.cardCode,
                        code:token
                    };
                }
                data = await axios.post(
                    this.URL + "buy/card",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            if(data?.data?.code != 1&&data?.data?.code != 0){
                this.$notify.error({
                    title: "提示",
                    message: data?.data?.msg,
                });
                return;
            }
            if(!isConfirm && data?.data?.code == 0){
                this.alertContent = data?.data?.msg;
                return;
            }
            this.$notify.success({
                title: "提示",
                message: "卡密使用成功",
            });
            this.form = {
                code:"",
                cardCode:"",
            };
            this.isConfirm = false;
            this.closeConfirm();             
            this.$emit("finish",true);
        }
    },
    mounted(){
    },
}
</script>

<style scoped lang="scss" src="@/assets/css/lib/useCard.scss"></style>