<template>
    <div id="userData" class="card">
        <div class="cardTitle">账号信息</div>
        <div :class="loading?'data loadingLocal':'data'">
            <div class="item">
                <label>用户名</label>
                <div class="value">{{userInfo?.username ?? '未知用户'}}</div>
            </div>
            <div class="item">
                <label>用户组</label>
                <div class="value">{{ userInfo?.group_info?.name ?? '未知用户组'}}</div>
            </div>
            <div class="item">
                <label>带宽限制</label>
                <div class="value">{{ userInfo?.frp_bandwidth ?? '未知' }}</div>
            </div>
            <div class="item">
                <label>基准隧道</label>
                <div class="value">{{ userInfo?.frp_tunnel_count ?? '0' }}</div>
            </div>
            <div class="item">
                <label>扩展隧道</label>
                <div class="value">{{ userInfo?.extend_tunnel_count ?? '0' }}</div>
            </div>
            <div class="item">
                <label>剩余隧道</label>
                <div class="value">{{ userInfo?.remaining_tunnel_count ?? '0' }}</div>
            </div>
            <div class="item">
                <label>基准流量</label>
                <div class="value">{{ trafficAmount(userInfo?.frp_traffic_amount ?? null) }}</div>
            </div>
            <div class="item">
                <label>扩展流量</label>
                <div class="value">{{ trafficAmount(userInfo?.extend_traffic_amount ?? null) }}</div>
            </div>
            <div class="item">
                <label>剩余流量</label>
                <div class="value">{{ trafficAmount(userInfo?.remaining_traffic_amount ?? null) }}</div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'userData',
    props: {
        userInfo:{
            type:Object,
            default:function(){
                return null
            }
        }
    },
    watch: { 
        userInfo:function(){
            this.loading = false;
        }
    },
    data: function () {
        return {
            loading:false,
        }
    },
    methods: {
        //流量换算
        trafficAmount(val) {
            if(!val){
                return "0MB";
            }
            return (val / 1024).toFixed(2) + "MB";
        }
    },
    mounted() {
        this.loading = true;
    },
}
</script>

<style lang="scss" scoped src="@/assets/css/lib/userData.scss"></style>