<template>
    <div id="downloadPage" class="page">
        <div :class="loading?'card loadingLocal':'card'">
            <div class="cardTitle">软件下载</div>

            <div class="top">
                <div :class="key==activeKey?'item active':'item'" v-for="(item,key) in data" :key="'soft_'+item.id" @click="changeClinet(item,key)">
                    <div class="row">
                        <img class="icon" :src="backendUrl+item.img"/>
                        <div class="name">
                            {{ item.name }}
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div :class="loading?'card loadingLocal':'card'">
            <div class="bottom">
                <div class="cardTitle">客户端说明</div>
                <div class="btnGroup">
                    <buttonLib text="下载" @click="linkTo"/>
                    <buttonLib class="help" text="帮助" @click="$router.replace('/help')"/>
                </div>
                <div class="clinetContent" v-html="clinetContent"></div>
            </div>
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import buttonLib from '@/components/lib/unit/buttonLib.vue';

export default {
    name: 'downloadPage',
    components: {
        buttonLib
    },
    data() {
        return {
            URL:window.config.apiUrl,
            backendUrl:window.config.backendUrl,
            data:[],
            axiosC: new AbortController(),    //请求列表 用于取消请求
            loading:false,
            clinetContent:"",
            link:"",
            activeKey:0,
        }
    },
    methods: {
        linkTo(){
            this.$root.linkTo(this.link,"_blank");
        },
        changeClinet(item,key){
            this.clinetContent = item.content;
            this.link = item.download;
            this.activeKey = key;
        },
        async getData(){
            this.loading = true;
            let data = null;
            try {
                data = await axios.get(
                    this.URL + "content/software",
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: "提示",
                    message: data?.data?.msg,
                });
                return;
            }
            this.data = data?.data?.data;
            if(this.data.length > 0){
                this.clinetContent = this.data[0].content;
                this.link = this.data[0].download;
            }
        },
    },
    mounted() {
        this.$nextTick(() => { 
            this.getData();
        });
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/downloadPage.scss">

</style>