<template>
    <div class="pageLib">
        <i :class="'icon el-icon-caret-left'+(selected>1?'':' disabled')" @click="prePage"></i>
        
        <ul v-if="data?.length > 0">
            第
            <li v-for="value in data" :key="'page_'+value" @click="select(value)">
                <div :class="selected==value?'num active':'num'">{{ value }}</div>
            </li>
            页
        </ul>

        <ul v-else>
            第0页
        </ul>
        
        <i :class="'icon el-icon-caret-right'+(selected<total?'':' disabled')" @click="nextPage"></i>
    </div>
</template>

<script>
export default {
    name: 'pageLib',
    props:{
        total:{ type:Number, default:20 },
        limit:{ type:Number, default:10 },
        page:{ type:Number, default:1 },
    },
    data(){
        return{
            selected: this.page,
            data:[], // 存放页码和省略号
        }
    },
    watch:{
        total(){
            this.formatterData();
        },
        selected(val){
            this.$emit("change",val);
        },
        page(){
            this.selected = this.page;
        }
    },
    methods:{ 
        formatterData(){
            this.data = [];
            const { total, limit, selected } = this;

            if (total <= limit) {
                for (let i = 1; i <= total; i++) {
                    this.data.push(i);
                }
                return;
            }
            const result = [];
            result.push(1);
            let left = selected - Math.floor((limit - 3) / 2); 
            let right = selected + Math.floor((limit - 3) / 2);
            if (left <= 2) {
                left = 2;
                right = limit - 2; // 留1和最后1
            }
            if (right >= total - 1) {
                right = total - 1;
                left = total - (limit - 3);
            }

            if (left > 2) {
                result.push("...");
            }

            for (let i = left; i <= right; i++) {
                result.push(i);
            }

            if (right < total - 1) {
                result.push("...");
            }
            result.push(total);
            this.data = result;
        },

        select(value){
            if(value === "...") return; // 省略号不可点
            if(this.selected !== value){
                this.selected = value;
                this.formatterData();
            }
        },
        prePage(){
            if(this.selected > 1){
                this.selected--;
                this.formatterData();
            }            
        },
        nextPage(){
            if(this.selected < this.total){
                this.selected++;
                this.formatterData();
            }       
        }
    },
    mounted(){
        this.formatterData();
    },
}
</script>

<style lang="scss" scoped src="@/assets/css/lib/unit/pageLib.scss">

</style>