<template>
    <div id="buttonLib" :class="btnClass">
        <button :class="classList" :style="sizeStyle" :disabled="disabled" @click="clickEvent">
            <div class="text">{{text}}</div>
        </button>
    </div>
</template>

<script>
export default{
    props:{
        btnClass:{
            type:String,
            default:""
        },
        text:{
            type:String,
            default:"这是一个按钮"
        },
        disabled:{
            type:Boolean,
            default:false
        },
        isActive:{
            type:Boolean,
            default:false
        },
        size:{
            type:String,
            default:"normal"
        }
    },
    watch:{
        isActive:{
            handler(val){
                if(val){
                    this.classList += " active"
                }
                else{
                    this.classList = this.classList.replace(" active","")
                }
            },
            deep:true,
            immediate:true
        },
        disabled:{
            handler(val){
                if(val){
                    this.classList += " disabled"
                }
                else{
                    this.classList = this.classList.replace(" disabled","")
                }
            },
            deep:true,
            immediate:true
        },
        size:{
            handler(val){
                if(val == "small"){
                    this.sizeStyle = "padding:5px";
                    return;
                }
                if(val == "large"){
                    this.sizeStyle = "padding:15px";
                    return;
                }
                this.sizeStyle = "";
            },
            deep:true,
            immediate:true
        }
    },
    data(){
        return{
            classList:"qhbtn",
            sizeStyle:"",
        }
    },
    methods:{
        clickEvent:function(){
            if(this.disabled){
                return;
            }
            this.$emit("click");
        }
    },
    mounted(){

    },
    created(){

    }
}

</script>

<style scoped lang="scss" src="@/assets/css/lib/unit/buttonLib.scss">

</style>