<template>
    <div id="userCenterPage" class="page">
        <div class="mainContent">
          <div class="left">
            <userData :userInfo="userInfo"/>
            <chartBox name="trafficChart" title="流量统计"/>
          </div>
          <div class="right">
            <signIn :userInfo="userInfo" @finish="getUserInfo"/>
            <announceList :showMore="true"/>
          </div>
        </div>
    </div>
</template>

<script> 
import userData from "@/components/lib/userData.vue";
import signIn from "@/components/lib/signIn.vue";
import announceList from "@/components/lib/announceList.vue";
import chartBox from "@/components/lib/chartBox.vue";


export default {
  name: 'userCenterPage',
  components: {
    userData,
    signIn,
    announceList,
    chartBox,
  },
  data() {
    return {
      userInfo: null
    }
  },
  methods: {
    //获取用户信息
    async getUserInfo() {
      let res = await this.$root.checkLogin();
      if(!res){
        if(this.$route.path != "/"){
          this.$router.replace("/");
        }        
        this.$root.goLogin();
      }
      this.userInfo = res?.data ?? null;
    }
  },
  created() {
  },
  mounted() {
    this.$nextTick(() => {
      //获取用户信息
      this.getUserInfo();
    });
  },
}
</script>
<style lang="scss" scoped src="@/assets/css/view/userCenterPage.scss">

</style>