<template>
    <div id="mallPage" class="page">
        <div class="left">
            <!-- 套餐列表 -->
            <div class="cardTable card">
                <div class="cardTitle">套餐列表</div>
                <div :class="loading?'tableBox loadingLocal':'tableBox'">
                    <div class="packageGrid" v-if="!loading && packageData.length > 0">
                        <div class="packageItem" v-for="(item, key) in packageData" :key="'package_'+key">
                            <div class="packageHeader">
                                <div class="packageName">{{ item.name }}</div>
                            </div>
                            
                            <div class="packageContent" v-html="item.content"></div>
                            
                            <div class="packageFooter">
                                <div class="priceInfo">
                                    <div class="price">
                                        <span class="currency">¥</span>
                                        <span class="amount">{{ item.price }}</span>
                                        <span class="unit">/月</span>
                                    </div>
                                </div>
                                
                                <div class="packageActions">
                                    <buttonLib 
                                        btnClass="buyBtn" 
                                        text="立即购买" 
                                        @click="showBuyDialog(item)"/>
                                </div>
                            </div>
                        </div>
                    </div>
                    
                    <!-- 空状态 -->
                    <div class="emptyState" v-else-if="!loading">
                        <div class="emptyIcon">
                            <i class="el-icon-s-goods"></i>
                        </div>
                        <div class="emptyText">暂无套餐数据</div>
                        <div class="emptyDesc">请尝试调整筛选条件或稍后再试</div>
                    </div>
                    
                    <!-- 分页 -->
                    <div class="pagenation" v-if="!loading && total > 0">
                        <pageLib :total="Math.ceil(total / pageSize)" :page="currentPage" @change="handlePageChange"/>
                    </div>
                </div>
            </div>
        </div>

        <div class="right">
            <!-- 套餐搜索区域 -->
            <div class="cardTable card">
                <div class="cardTitle">套餐搜索</div>
                <div class="searchForm">
                    <!-- 套餐名称搜索 -->
                    <div class="formRow">
                        <label>套餐名称</label>
                        <input type="text" v-model="searchKeyword" placeholder="请输入套餐名称" />
                    </div>
                    
                    <!-- 价格筛选 -->
                    <div class="formRow">
                        <label>价格筛选</label>
                        <el-select v-model="priceFilter" placeholder="请选择价格范围" clearable>
                            <el-option label="全部价格" value=""></el-option>
                            <el-option label="0-50元" value="0-50"></el-option>
                            <el-option label="50-100元" value="50-100"></el-option>
                            <el-option label="100-200元" value="100-200"></el-option>
                            <el-option label="200元以上" value="200+"></el-option>
                        </el-select>
                    </div>
                    
                    <!-- 操作按钮 -->
                    <div class="formRow">
                        <div class="btnGroup">
                            <buttonLib btnClass="btn" text="搜索" @click="searchPackages"/>
                            <buttonLib btnClass="btn" text="重置" @click="resetFilters"/>
                            <buttonLib btnClass="btn" text="刷新" @click="reloadData"/>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <!-- 购买对话框 -->
        <dialogLib 
            v-if="isShowBuyDialog" 
            title="购买套餐" 
            height="auto" 
            max-width="500px" 
            :hide-with-mask="false"
            @hide="closeBuyDialog">
            <div class="buyDialog" v-if="selectedPackage">
                <div class="packageInfo">
                    <div class="packageName">{{ selectedPackage.name }}</div>
                    <div class="packagePrice">
                        <span class="currency">¥</span>
                        <span class="amount">{{ selectedPackage.price }}</span>
                        <span class="unit">/月</span>
                    </div>
                </div>
                
                <div class="buyForm">
                    <div class="packageDescription">
                        <div class="label">套餐说明：</div>
                        <div class="description" v-html="selectedPackage.content"></div>
                    </div>
                </div>
                
                <div class="buyActions">
                    <buttonLib btnClass="btn cancel" text="取消" @click="closeBuyDialog"/>
                    <buttonLib btnClass="buyBtn" text="确认购买" @click="confirmBuy"/>
                </div>
            </div>
        </dialogLib>

    </div>
</template>

<script>
import axios from 'axios';
import buttonLib from '@/components/lib/unit/buttonLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';
import pageLib from '@/components/lib/unit/pageLib.vue';

export default {
    name: 'MallPage',
    components: {
        buttonLib,
        dialogLib,
        pageLib
    },
    data() {
        return {
            URL: window.config.apiUrl,
            loading: false,
            userInfo: null,
            
            // 套餐数据
            packageData: [],
            allPackageData: [], 
            currentPage: 1,
            pageSize: 10,
            total: 0,
            totalPage: 1,
            
            // 缓存控制
            cacheData: {
                packages: null,
                lastFetchTime: 0,
                cacheExpiry: 5 * 60 * 1000, 
                isRefreshing: false,
                version: 1 
            },
            
            // 筛选条件
            searchKeyword: "",
            priceFilter: "",
            
            // 购买相关
            selectedPackage: null,
            isShowBuyDialog: false,
            buyForm: {
                packageId: ""
            },
            axiosC: new AbortController(),
            searchTimeout: null
        }
    },
    watch: {
        priceFilter: function() {
            this.applyFilters();
        }
    },
    methods: {
        // 获取用户信息
        async getUserInfo() {
            let res = await this.$root.checkLogin();
            if (!res) {
                if (this.$route.path != "/") {
                    this.$router.replace("/");
                }
                this.$root.goLogin();
                return;
            }
            this.userInfo = res?.data ?? null;
        },
        // 检查缓存是否有效
        isCacheValid() {
            const now = Date.now();
            return this.cacheData.packages && 
                   (now - this.cacheData.lastFetchTime) < this.cacheData.cacheExpiry;
        },
        
        // 获取套餐数据（带缓存）
        async getPackageData(page = 1, limit = 9999, forceRefresh = false, externalLoadingControl = false) {
            // 如果缓存有效且不是强制刷新，直接使用缓存
            if (!forceRefresh && this.isCacheValid()) {
                this.allPackageData = this.cacheData.packages;
                this.total = this.allPackageData.length;
                this.totalPage = Math.ceil(this.total / this.pageSize);
                this.applyFilters();
                return;
            }
            // 如果正在刷新，避免重复请求
            if (this.cacheData.isRefreshing) {
                return;
            }
            
            this.cacheData.isRefreshing = true;
            if (!externalLoadingControl) {
                this.loading = true;
            }
            let data = null;
            
            try {
                data = await axios.get(this.URL + "content/package", {
                    params: { page, limit },
                    signal: this.axiosC.signal
                });
            } catch (err) {
                if (!externalLoadingControl) {
                    this.loading = false;
                }
                this.cacheData.isRefreshing = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    // 请求被取消，不需要处理错误
                } else {
                    this.$root.netError(err);
                }
                return;
            }
            
            if (!externalLoadingControl) {
                this.loading = false;
            }
            this.cacheData.isRefreshing = false;
            
            if (data?.data?.code != 1) {
                this.$notify.error({
                    title: "提示",
                    message: data?.data?.msg || "获取套餐数据失败",
                });
                return;
            }
            
            const responseData = data?.data?.data ?? {};
            this.allPackageData = responseData.data ?? [];
            this.total = responseData.total ?? this.allPackageData.length;
            this.totalPage = responseData.last_page ?? 1;
            
            // 更新缓存
            this.cacheData.packages = [...this.allPackageData];
            this.cacheData.lastFetchTime = Date.now();
            
            // 应用筛选
            this.applyFilters();
        },
        
        // 应用筛选
        applyFilters() {
            let filteredData = [...this.allPackageData];
            
            // 按名称搜索
            if (this.searchKeyword.trim()) {
                filteredData = filteredData.filter(item => 
                    item.name.toLowerCase().includes(this.searchKeyword.toLowerCase())
                );
            }
            
            // 按价格筛选
            if (this.priceFilter) {
                filteredData = filteredData.filter(item => {
                    const price = parseFloat(item.price);
                    switch (this.priceFilter) {
                        case '0-50':
                            return price >= 0 && price <= 50;
                        case '50-100':
                            return price > 50 && price <= 100;
                        case '100-200':
                            return price > 100 && price <= 200;
                        case '200+':
                            return price > 200;
                        default:
                            return true;
                    }
                });
            }
            
            
            this.total = filteredData.length;
            const startIndex = (this.currentPage - 1) * this.pageSize;
            const endIndex = startIndex + this.pageSize;
            this.packageData = filteredData.slice(startIndex, endIndex);
        },
        
        // 搜索防抖
        debouncedSearch: (function () {
            let timer = null;
            return function () {
                if (timer) clearTimeout(timer);
                timer = setTimeout(() => {
                    this.searchPackages();
                }, 500);
            }
        })(),
        
        // 搜索套餐
        searchPackages() {
            this.currentPage = 1;
            this.applyFilters();
        },
        
        // 重置筛选
        resetFilters() {
            this.searchKeyword = "";
            this.priceFilter = "";
            this.currentPage = 1;
            this.applyFilters();
        },
        
        // 刷新数据
        async reloadData() {
            const startTime = Date.now();
            this.loading = true;
            
            try {
                await this.getPackageData(1, 9999, true, true);
            } finally {
                // 确保加载状态至少显示0.5秒
                const elapsedTime = Date.now() - startTime;
                const remainingTime = Math.max(0, 500 - elapsedTime);
                
                if (remainingTime > 0) {
                    setTimeout(() => {
                        this.loading = false;
                    }, remainingTime);
                } else {
                    this.loading = false;
                }
            }
        },
        
        // 分页处理
        handlePageChange(page) {
            this.currentPage = page;
            this.applyFilters();
        },
        
        // 显示购买对话框
        showBuyDialog(packageItem) {
            this.selectedPackage = packageItem;
            this.buyForm.packageId = packageItem.id; 
            this.isShowBuyDialog = true;
        },
        
        // 关闭购买对话框
        closeBuyDialog() {
            this.isShowBuyDialog = false;
            this.selectedPackage = null;
            this.buyForm = {
                packageId: ""
            };
            this.$nextTick(() => {
                this.$root.enableMask(false);
            });
        },
        
        
        // 确认购买
        async confirmBuy() {
            if (!this.selectedPackage) {
                this.$notify.error({
                    title: '提示',
                    message: '请选择套餐',
                    offset: this.$root.notifyOffet
                });
                return;
            }
            
            
            // 检查套餐是否有链接
            if (!this.selectedPackage.link) {
                this.$notify.error({
                    title: '提示',
                    message: '该套餐暂无可用的购买链接，请联系管理员',
                    offset: this.$root.notifyOffet
                });
                return;
            }
            
            // 先保存链接，再关闭对话框
            const packageLink = this.selectedPackage.link;
            
            // 关闭购买对话框
            this.closeBuyDialog();
            
            // 打开套餐链接
            try {
                window.open(packageLink, '_blank');
            } catch (err) {
                this.$notify.error({
                    title: '提示',
                    message: '无法打开购买链接，请联系管理员检查链接',
                    offset: this.$root.notifyOffet
                });
            }
        },
        
        // 检查自动购买对话框
        checkAutoBuyDialog() {
            const packageName = this.$route.query.package;
            const packagePrice = this.$route.query.price;
            if (packageName && packagePrice) {
                this.$nextTick(() => {
                    setTimeout(() => {
                        this.autoOpenBuyDialog(packageName, packagePrice);
                    }, 1000);
                });
            }
        },
        
        // 自动打开购买对话框
        autoOpenBuyDialog(packageName, packagePrice) {
            const targetPackage = this.allPackageData.find(pkg => 
                pkg.name === packageName && pkg.price == packagePrice
            );
            if (targetPackage) {
                this.showBuyDialog(targetPackage);
                this.$router.replace({ path: '/mall' });
            } else {
                const nameMatchPackage = this.allPackageData.find(pkg => 
                    pkg.name === packageName
                );
                if (nameMatchPackage) {
                    this.showBuyDialog(nameMatchPackage);
                    this.$router.replace({ path: '/mall' });
                }
            }
        },
        
        // 跳转到套餐链接
        goToPackageLink(packageItem) {
            if (packageItem.link) {
                this.$root.linkTo(packageItem.link, '_blank');
            }
        },
        
        // 清理缓存
        clearCache() {
            this.cacheData.packages = null;
            this.cacheData.lastFetchTime = 0;
            this.cacheData.isRefreshing = false;
        }
    },
    mounted() {
        this.$nextTick(() => {
            this.getUserInfo();
            this.getPackageData();
            this.checkAutoBuyDialog();
        });
    },
    beforeDestroy() {
        // 清理资源
        this.axiosC.abort();
        if (this.searchTimeout) {
            clearTimeout(this.searchTimeout);
        }
        this.clearCache();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/mallPage.scss">

</style>