<template>
    <div id="domainAuthPage" class="page">
        <div class="card">
            <div class="cardTitle">域名审核</div>

            <div class="toolsBar">
                <div class="row">
                    <el-alert
                        title="本页面不是提交备案申请的页面，千鹤FRP不提供任何备案服务"
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title="创建隧道时仅可使用此处显示已经通过审核的域名"
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title="提交审核时请输入已经备案完成、可以在工信部系统查到备案信息的域名"
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title="提交审核后 系统将在5-10分钟内自动对域名备案情况进行审核"
                        type="info"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title="若审核未通过但工作人员没有手动拒绝 则系统将实时监控域名备案状态 直到备案通过或用户手动删除"
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        :title="'每个用户仅可使用'+limit+'个域名 您可用通过删除域名来释放额度'"
                        type="info"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
            </div>

            <div class="toolsBar">
                <div class="row">
                    <div class="btnGroup">
                        <buttonLib btnClass="btn" text="提交审核" @click="openSubmitBox"/>
                        <buttonLib btnClass="btn" text="刷新" @click="getData()"/>
                    </div>
                </div>
            </div>

            <div :class="loading?'tableBox loadingLocal':'tableBox'">
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column prop="domain" label="域名"  :resizable="false"></el-table-column>

                    <el-table-column prop="status" label="状态"  :resizable="false">
                        <template slot-scope="scope">
                            <el-tag v-if="scope.row.status == 0" type="success">已通过</el-tag>
                            <el-tag v-if="scope.row.status == 1" type="info">暂未通过</el-tag>
                            <el-tag v-if="scope.row.status == 2" type="danger">已拒绝</el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column prop="createtime" label="提交时间"  :resizable="false">
                        <template slot-scope="scope">
                            {{ $root.getDate(scope.row.createtime) }}
                        </template>
                    </el-table-column>

                    <el-table-column prop="updatetime" label="最后审查时间"  :resizable="false">
                        <template slot-scope="scope">
                            {{ $root.getDate(scope.row.updatetime) }}
                        </template>
                    </el-table-column>

                    <el-table-column fixed="right" label="操作" width="100"  :resizable="false">
                        <template slot-scope="scope">
                            <div class="btnGroup">
                                <buttonLib text="删除" class="btn" @click="del(scope.row)"/>
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
            </div>

            <!-- 二次确认弹窗S -->
            <dialogLib ref="dialog" title="删除确认" height="auto" max-width="400px" v-if="isShowConfirm" :isShowClose="false" @hide="cancelDel">
                <div class="confirm">
                    <div class="row">
                        <div class="title">确定要删除 <span style="color: red;">{{ delName }}</span> 吗?</div>
                    </div>
                    <div class="row">
                        <el-alert
                            title="已审核的域名删除后重新提交,需要重新审核."
                            type="error"
                            center
                            :closable="false">
                        </el-alert>
                    </div>
                    <div class="row">
                        <buttonLib btnClass="btn yes" text="确定" @click="doDel"/>
                        <buttonLib btnClass="btn no" text="取消" @click="cancelDel"/>
                    </div>
                </div>
            </dialogLib>
            <!-- 二次确认弹窗E -->

            <!-- 提交窗口S -->
                <dialogLib ref="dialog" title="提交域名审核" height="auto" max-width="400px" :isShowClose="!submitLoading" v-if="isShowSubmitBox" @hide="closeSubmitBox">
                    <div :class="submitLoading?'form loadingLocal':'form'">
                        <div class="row">
                            <el-alert
                                title="请输入已经备案完成、可以在工信部系统查到备案信息的域名"
                                type="error"
                                center
                                :closable="false">
                            </el-alert>
                        </div>
                        <div class="row">
                            <div class="title">域名</div>
                            <div class="inputBox">
                                <div class="pre">http://</div>
                                <input class="hasPre" v-model="form.domain" placeholder="请输入域名"/>
                            </div>
                        </div>
                        <div class="row">
                            <buttonLib btnClass="btn" text="提交" @click="submit"/>
                        </div>
                    </div>
                </dialogLib>
             <!-- 提交窗口E -->
        </div>
    </div>
</template>

<script>
import axios from 'axios';
import buttonLib from '@/components/lib/unit/buttonLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';

export default {
    name: 'domainAuthPage',
    components: {
        buttonLib,
        dialogLib
    },
    data() {
        return {
            loading:false,
            submitLoading:false,
            tableData:[],
            URL:window.config.apiUrl,
            limit:0,
            userInfo:null,
            axiosC: new AbortController(),    //请求列表 用于取消请求
            isShowConfirm:false,
            delName:"",
            delId:"",
            isShowSubmitBox:false,
            form:{
                domain:"",
            }
        }
    },
    methods: {
        //提交审核
        async submit(){ 
            this.loading = true;
            let data = null;
            try {
                data = await axios.post(
                    this.URL + "frp/domainAuth",
                    {
                        domain:this.form.domain,
                    },
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: '提示',
                message: "提交成功",
            });
            this.closeSubmitBox();
            this.getData();
        },
        //关闭提交窗口
        closeSubmitBox(){
            this.$refs?.dialog?.hide();
            this.isShowSubmitBox = false;
            this.form.domain = "";
        },
        //打开提交窗口
        openSubmitBox(){
            this.isShowSubmitBox = true;
        },
        //取消删除
        cancelDel(){
            this.$refs.dialog.hide();
            this.isShowConfirm = false;
            this.delName = "";
            this.delId = "";
        },
        //删除二次确认
        del(row){
            this.delName = row.domain;
            this.isShowConfirm = true;
            this.delId = row.id;
        },
        //删除域名
        async doDel(){
            this.loading = true;
            let data = null;
            this.$refs.dialog.hide();
            this.isShowConfirm = false;
            this.delName = "";
            try {
                data = await axios.post(
                    this.URL + "frp/delDomain",
                    {
                        id:this.delId,
                    },
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
                this.delId = "";
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }
            this.$notify.success({
                title: '提示',
                message: "删除成功",
            });
            this.getData();
        },
        //获取数据
        async getData() {
            this.loading = true;
            this.tableData = [];
            let data = null;
            try {
                data = await axios.get(
                    this.URL + "frp/domainsList",
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.loading = false;
            if(data?.data?.code != 1){
                this.$notify.error({
                    title: '提示',
                    message: data?.data?.msg,
                });
                return;
            }
            this.tableData = data?.data?.data?.data;
            this.limit = data?.data?.data?.limit;
        },
    },
    mounted() {
        this.$nextTick(() => { 
            this.getData();
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/domainAuthPage.scss">

</style>