<template>
    <div id="signin" :class="loading?'card loadingLocal':'card'">
        <div class="cardTitle">每日签到</div>
        <div class="content">立即签到获取每日流量奖励!</div>
        <div class="already" v-if="isToday">
            <div class="right"></div>   
            <div class="text">今日已签到</div>            
        </div>
        <div class="btn" v-if="!isToday" @click="signin">
            <div class="text">签到处</div>
            <div class="fingerPrint"></div>           
        </div>
    </div>
</template>

<script>
import axios from 'axios';

export default {
    name: 'signIn',
    props: {
        userInfo:{
            type:Object,
            default:function(){
                return null
            }
        }
    },
    watch:{ 
        userInfo:function(){
            this.loading = false;
        }
    },
    data() {
        return {
            URL:window.config.apiUrl,
            loading:false,
            axiosC:new AbortController(),    //请求列表 用于取消请求
        }
    },
    computed: {
        isToday(){
            let signInTime = this.userInfo?.sign_time ?? null;
            if(signInTime==null){
                return false
            }
            //时间戳转换为日期
            let signInDate = new Date(signInTime * 1000);
            //判断是否为今天
            return signInDate.getFullYear() == new Date().getFullYear() && signInDate.getMonth() == new Date().getMonth() && signInDate.getDate() == new Date().getDate();
        }
    },
    methods: {
        //签到
        async signin(){
            this.$root.loading(true);
            let token = this.$root.getLocalToken();
            if(!token){
                return;
            }
            let header = {
                headers:{token:token},
                signal: this.axiosC.signal
            }
            let res = null;
            try{
                res = await axios.post(this.URL+'user/sign',{},header);
            }
            catch(err){
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else{
                    this.$root.loading(false);
                    this.$root.netError(err);
                }                     
                return;
            }
            if(res?.data.code == -1){
                this.$notify.error({
                    title: '提示',
                    message: res?.data.msg ?? '未知错误',
                    offset:this.$root.notifyOffet
                });
                return;
            }
            //通知父组件
            this.$emit("finish");
            this.$root.loading(false);
            this.$notify.success({
                title: '提示',
                message: res?.data.msg ?? '签到成功',
                offset:this.$root.notifyOffet
            });
        }
    },
    mounted() {
        this.loading = true
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>

<style lang="scss" scoped src="@/assets/css/lib/signIn.scss"></style>