import Vue from 'vue';
import Router from 'vue-router'; 
import HomePage from '@/components/view/HomePage.vue';
import UserCenterPage from '@/components/view/UserCenterPage.vue';
import TunnelPage from '@/components/view/TunnelPage.vue';
import DownloadPage from '@/components/view/DownloadPage.vue';
import DomainAuthPage from '@/components/view/DomainAuthPage.vue';
import TicketPage from '@/components/view/TicketPage.vue';
import UserPage from '@/components/view/UserPage.vue';
import MallPage from '@/components/view/MallPage.vue';
import HelpPage from '@/components/view/HelpPage.vue';
import ServerStatusPage from '@/components/view/ServerStatusPage.vue';

Vue.use(Router);

const routes = [
  {
    path: '/',
    name: 'HomePage',
    component: HomePage
  },
  {
    path: '/usercenter',
    name: 'UserCenterPage',
    component: UserCenterPage
  },
  {
    path: '/tunnel',
    name: 'TunnelPage',
    component: TunnelPage
  },
  {
    path: '/servers',
    name: 'ServerStatus',
    component: ServerStatusPage
  },
  {
    path: '/download',
    name: 'DownloadPage',
    component: DownloadPage
  },
  {
    path: '/domainauth',
    name: 'DomainAuthPage',
    component: DomainAuthPage
  },
  {
    path: '/ticket',
    name: 'TicketPage',
    component: TicketPage
  },
  {
    path: '/mall',
    name: 'MallPage',
    component: MallPage
  },
  {
    path: '/user',
    name: 'UserPage',
    component: UserPage
  },
  {
    path: '/help',
    name: 'HelpPage',
    component: HelpPage
  },
  {
    path: '*',
    redirect: '/'
  }
];

const router = new Router({
  mode: 'hash', // 可以选择使用 'hash' 或 'history'
  routes
});

export default router;
