<template>
    <div id="tunnelPage" class="page">
        <div class="card">
            <div class="cardTitle">隧道列表</div>
            <div class="toolsBar">
                <div class="row">
                    <div class="title">搜索</div>
                </div>
                <div class="row">
                    <el-select v-model="protocolFilter" multiple collapse-tags placeholder="请选择协议">
                        <el-option v-for="item in protocols" :key="item.value" :label="item.label" :value="item.value">
                        </el-option>
                    </el-select>
                    <input type="text" v-model="nameFilter" placeholder="请输入隧道名称" />
                    <input type="text" v-model="localAddrFilter" placeholder="请输入本地地址" />
                    <input type="number" v-model="remoteAddrFilter" placeholder="请输入本地端口" />
                </div>
                <div class="row">
                    <div class="btnGroup">
                        <buttonLib btnClass="btn" text="新建隧道" :disabled="userInfo?.remaining_tunnel_count == 0" @click="openServerSelect()"/>
                        <buttonLib btnClass="btn" text="刷新" @click="getData(limit,1)"/>
                    </div>
                </div>
                <div class="row">
                    <el-alert
                        v-if="userInfo?.remaining_tunnel_count > 0"
                        :title="'您还可以新建' + userInfo?.remaining_tunnel_count + '条隧道'"
                        type="warning"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                    <el-alert
                        v-if="userInfo?.remaining_tunnel_count == 0"
                        :title="'您已达到隧道数量限制 无法再新建任何隧道了'"
                        type="error"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
                <div class="row">
                    <el-alert
                        title="当您删除隧道后 域名占用不会马上同步 请耐心等待5-10分钟后再尝试使用相同的域名"
                        type="warning"
                        effect="dark"
                        :closable="false">
                    </el-alert>
                </div>
            </div>

            <div :class="loading?'tableBox loadingLocal':'tableBox'">
                <el-table :data="tableData" border style="width: 100%">
                    <el-table-column prop="name" label="名称" width="100" :resizable="false">
                    </el-table-column>

                    <el-table-column prop="status" label="状态" :resizable="false">
                        <template slot-scope="scope">
                            <el-tag v-if="scope.row.status === 'normal'" type="success">已启用</el-tag>
                            <el-tag v-else type="info">已停用</el-tag>
                        </template>
                    </el-table-column>

                    <el-table-column prop="server_name" label="服务器名称" width="100" :resizable="false">
                    </el-table-column>

                    <el-table-column prop="protocol" label="协议">
                    </el-table-column>

                    <el-table-column prop="domains" label="自定义域名" width="300" :resizable="false">
                        <template slot-scope="scope">
                            <div v-for="(domain,key) in $root.formatterDomains(scope.row.domains)" :key="'tunnel_domain_'+key">
                                <a                                
                                :href="scope.row.protocol+'://'+domain"
                                target="_blank"
                                >
                                    {{ scope.row.protocol+'://'+domain }}
                                </a>
                            </div>
                        </template>
                    </el-table-column>

                    <el-table-column prop="local_addr" label="本地地址" :resizable="false">
                    </el-table-column>

                    <el-table-column prop="local_port" label="本地端口" :resizable="false">
                    </el-table-column>

                    <el-table-column fixed="right" label="操作" width="200" :resizable="false">
                        <template slot-scope="scope">
                            <div class="btnGroup">
                                <buttonLib @click="edit(scope.row)" btnClass="rowBtn" size="small" text="编辑" />
                                <buttonLib @click="disable(scope.row)" btnClass="rowBtn" size="small" :text="scope.row.status=='normal'?'停用':'启用'" />
                                <buttonLib @click="delConfirm(scope.row)" btnClass="rowBtn" size="small" text="删除" />
                            </div>
                        </template>
                    </el-table-column>
                </el-table>
                <div class="pagenation">
                    <pageLib :total="totalPage" :page="page" @change="changePage"/>
                </div>
            </div>
        </div>

        <!-- 服务器选择窗S -->
        <dialogLib title="选择服务器" height="70vh" width="100vw" max-width="1000px" v-if="isShowServerSelectBox" @hide="closeServerSelectBox">
            <div class="serverList">
                <div class="toolsBar">
                    <div class="row">
                        <div class="title">搜索</div>
                    </div>
                    <div class="row">
                        <input placeholder="请输入服务器名称" v-model="newNameFilter"/>

                        <el-select v-model="newProtocolFilter" multiple collapse-tags placeholder="请选择协议">
                            <el-option v-for="item in protocols" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>

                        <el-select v-model="newUserGroupFilter" collapse-tags placeholder="请选择用户组">
                            <el-option label="全部" value="all"></el-option>
                            <el-option v-for="item in userGroups" :key="item.value" :label="item.label" :value="item.value">
                            </el-option>
                        </el-select>
                    </div>
                </div>
                <div class="serverData">
                    <div class="item" v-for="item in serverList" :key="'server_'+item.id" @click="newTunnel(item)">
                        <div class="name">{{ item.name }}</div>
                        <div class="group">
                            <el-tag type="info" v-for="text in item.group_arr" :key="'group_'+item.id+'_'+text">{{ text }}</el-tag>
                        </div>
                        <div class="protocals">
                            <el-tag type="info" v-for="text in item.protocol" :key="'protocol_'+item.id+'_'+text">{{ text }}</el-tag>
                        </div>
                        <div class="runtime">
                            <span class="label">连续运行:</span>{{ item.payload_time }}
                        </div>
                        <div class="payload">
                            <span class="label">当前负载:</span>{{ item.payload_load_5 }}%
                        </div>
                        <div class="portused" v-if="item.payload_json.total_ports > 0">
                            <span class="label">已用端口:</span>{{ item.payload_json.used_ports }}/{{ item.payload_json.total_ports }}
                        </div>
                        <div class="portused" v-if="item.payload_json.total_ports == 0">
                            <span class="label">已用端口:</span>{{ item.payload_json.used_ports }}/无限
                        </div>
                    </div>
                </div>
            </div>
         </dialogLib>
        <!-- 服务器选择窗E -->

        <!-- 编辑弹窗S -->
         <dialogLib title="隧道编辑" height="70vh" max-width="400px" v-if="editId" @hide="closeEdit">
            <div class="editFrom">
                <div class="row">
                    <div class="title">状态</div>
                    <div class="btnGroup">
                        <buttonLib
                        btnClass="btn"
                        size="small"
                        :isActive="editData?.status=='normal'"
                        text="启用"
                        @click="editData.status='normal'"
                        />
                        <buttonLib
                        btnClass="btn"
                        size="small"
                        :isActive="editData?.status=='hidden'"
                        text="停用"
                        @click="editData.status='hidden'"
                        />
                    </div>
                </div>
                <div class="row">
                    <div class="title">隧道名称</div>
                    <input v-model="editData.name" placeholder="请输入隧道名称"/>
                </div>
                <div class="row">
                    <div class="title">本地IP</div>
                    <input v-model="editData.local_addr" placeholder="请输入本地IP"/>
                </div>
                <div class="row">
                    <div class="title">本地端口</div>
                    <input v-model="editData.local_port" placeholder="请输入本地端口"/>
                </div>
                <div class="row">
                    <div class="title">备注</div>
                    <textarea rows="5" v-model="editData.remark" placeholder="请输入备注"></textarea>
                </div>
                <div class="row">
                    <buttonLib btnClass="btn" text="保存" @click="saveEdit"/>
                </div>
            </div>
         </dialogLib>
         <!-- 编辑弹窗E -->

        <!-- 新建弹窗S -->
        <dialogLib title="新建隧道" height="70vh" max-width="400px" v-if="isShowNew" @hide="closeNew" :hideWithMask="isShowServerSelectBox?false:true">
            <div class="editFrom">
                <div class="row">
                    <div class="title req">隧道名称</div>
                    <input v-model="editData.name" placeholder="请输入隧道名称"/>
                </div>
                <div class="row">
                    <div class="title req">本地IP</div>
                    <input v-model="editData.local_addr" placeholder="请输入本地IP"/>
                </div>
                <div class="row">
                    <div class="title req">本地端口</div>
                    <input v-model="editData.local_port" placeholder="请输入本地端口"/>
                </div>
                <div class="row">
                    <div class="title req">服务器</div>
                    <el-select v-model="editData.server_id" placeholder="请选择服务器" :disabled="true"> 
                        <el-option v-for="item in serverList" :key="item.id" :label="item.name" :value="item.id"> </el-option>
                    </el-select>
                </div>
                <div class="row">
                    <div class="title req">协议</div>
                    <el-select v-model="editData.protocol" placeholder="请选择协议"> 
                        <el-option v-for="item in serverProtocos" :key="item.value" :label="item.label" :value="item.value"> </el-option>
                    </el-select>
                </div>
                <div class="row" v-if="editData.protocol=='http'||editData.protocol=='https'">
                    <div class="title req">域名</div>
                    <div class="inputBox" v-for="(row,key) in domainsRow" :key="'domain_'+key">
                        <div class="pre">{{ editData.protocol }}://</div>
                        <input class="hasPre hasAppend" v-model="domainsRow[key].domain" placeholder="请输入域名"/>
                        <div class="append" @click="deleteDomain(key)">
                            删除
                        </div>
                    </div>
                    <div class="addBtn">
                        <buttonLib btnClass="btn" text="添加" @click="addDomain"/>
                    </div>
                </div>
                <div class="row">
                    <div class="title">备注</div>
                    <textarea rows="5" v-model="editData.remark" placeholder="请输入备注"></textarea>
                </div>
                <div class="row">
                    <buttonLib btnClass="btn" text="保存" @click="saveNew"/>
                </div>
            </div>
         </dialogLib>
        <!-- 新建弹窗E -->

         <!-- 二次确认弹窗S -->
          <dialogLib ref="confirm" title="删除确认" height="auto" max-width="300px" v-if="isShowConfirm" @hide="cancelDel(true)" :isShowClose="false">
            <div class="confirm">
                <div class="row">
                    <div class="title">确定要删除 <span style="color: red;">{{ delName }}</span> 吗？</div>
                </div>
                <div class="row">
                    <buttonLib btnClass="btn yes" text="确定" @click="del"/>
                    <buttonLib btnClass="btn no" text="取消" @click="cancelDel"/>
                </div>
            </div>
          </dialogLib>
         <!-- 二次确认弹窗E -->
    </div>
</template>

<script>
import axios from 'axios';

import buttonLib from '@/components/lib/unit/buttonLib.vue';
import pageLib from '@/components/lib/unit/pageLib.vue';
import dialogLib from '@/components/lib/unit/dialogLib.vue';

export default {
    name: 'tunnelPage',
    components: {
        buttonLib,
        pageLib,
        dialogLib
    },
    data() {
        return {
            isInit: true,   //标记初始化阶段
            userInfo:null,
            URL: window.config.apiUrl,
            tableData: [],
            protocols: [],
            protocolFilter: [],
            nameFilter: "",
            localAddrFilter: "",
            remoteAddrFilter: "",
            limit: 10,
            page: 1,
            totalPage: 0,
            loading:false,
            editId:null,
            editData:{},
            delId:null,
            delName:"",
            isShowConfirm:false,
            isShowNew:false,
            serverList:[],
            isShowServerSelectBox:false,
            axiosC: new AbortController(),    //请求列表 用于取消请求

            //新建隧道窗口S
            userGroups: [],
            newProtocolFilter: [],
            newUserGroupFilter: [],
            serverProtocos: [],
            newNameFilter: "",
            domainsRow:[],
            //新建隧道窗口E
        }
    },
    watch: {
        protocolFilter:function(){
            if(this.isInit){
                return;
            }
            this.debouncedGetData();
        },
        nameFilter:function(){
            if(this.isInit){
                return;
            }
            this.debouncedGetData();
        },
        localAddrFilter:function(){
            if(this.isInit){
                return;
            }
            this.debouncedGetData();
        },
        remoteAddrFilter:function(){
            if(this.isInit){
                return;
            }
            this.debouncedGetData();
        },
        newProtocolFilter:"debouncedGetServersData",
        newUserGroupFilter:"debouncedGetServersData",
        newNameFilter:"debouncedGetServersData",
    },
    methods: {
        //获取用户信息
        async getUserInfo() {
            let res = await this.$root.checkLogin();
            if(!res){
                if(this.$route.path != "/"){
                this.$router.replace("/");
                }        
                this.$root.goLogin();
            }
            this.userInfo = res?.data ?? null;
        },
        //删除域名按钮
        deleteDomain(index){
            this.domainsRow.splice(index,1);
        },
        //添加域名按钮
        addDomain(){
            if(this.domainsRow.length > 2){
                this.$notify.error({
                    title: '提示',
                    message: '最多只能添加3个域名',
                });
                return;
            }
            this.domainsRow.push({
                id:this.domainsRow.length,
                domain:""
            });
        },
        //获取服务器数据
        async getServerData(){
            let data = null;
            //过滤器S
            let protocolsFilterText = "";
            let arr = [];
            if(this.newProtocolFilter.length == 0){                
                this.protocols.forEach(element => {
                    arr.push(element.value);
                });
            }
            else{
                this.newProtocolFilter.forEach(element => {
                    arr.push(element);
                });
            }
            protocolsFilterText = arr.join(",");

            let newUserGroupFilterText = this.newUserGroupFilter;
            //过滤器E
            let postData = {};
            if(newUserGroupFilterText!=''&&newUserGroupFilterText!='all'){
                postData = {
                    protocol:protocolsFilterText,
                    group:newUserGroupFilterText,
                    name:this.newNameFilter,
                };
            }
            else{
                postData = {
                    protocol:protocolsFilterText,
                    name:this.newNameFilter,
                };
            }
            postData.limit = 1000;  //暂时显示1000个记录 服务器数量应该不会很多
            try{
                data = await axios.post(this.URL+"frp/serverList",
                    postData,
                    {
                        headers:{token:localStorage.getItem("token")},
                        signal:this.axiosC.signal
                    }
                );
            }
            catch(err){
                this.$root.netError(err);
            }
            this.serverList = data?.data?.data?.data;
            let selection = data?.data?.data?.selection;
            this.userGroups = selection?.goups;
            this.protocols = selection?.protocols;
        },
        //打开服务器选择窗
        openServerSelect(){
            this.isShowServerSelectBox = true;
            this.getServerData();
        },
        //关闭服务器选择窗
        closeServerSelectBox(){
            this.isShowServerSelectBox = false;
            this.$root.enableMask(false);
        },
        //新建隧道
        async saveNew(){
            //验空S
            if(
                !this.editData?.server_id||this.editData?.server_id==''||
                !this.editData?.protocol||this.editData?.protocol==''||
                !this.editData?.name||this.editData?.name==''||
                !this.editData?.local_addr||this.editData?.local_addr==''||
                !this.editData?.local_port||this.editData?.local_port==''||
                (this.editData?.protocol=='http'&&(!this.domainsRow||this.domainsRow.length==0))||
                (this.editData?.protocol=='https'&&(!this.domainsRow||this.domainsRow.length==0))
            ){
                this.$notify.error({ 
                    title: "提示",
                    message: '请填写必填项',
                });
                return;
            }
            //验空E

            this.$root.loading(true);
            let data = null;
            let postData = {
                serverID: this.editData?.server_id,
                name: this.editData?.name,
                localIP: this.editData?.local_addr,
                localPort: this.editData?.local_port,
                remark: this.editData?.remark,
                protocol: this.editData?.protocol,
            };
            if(this.editData?.protocol=='http'||this.editData?.protocol=='https'){
                let domainStr = "";
                this.domainsRow.forEach(item => {
                    domainStr += item.domain+",";
                });
                postData.domains = domainStr;
            }
            try {
                data = await axios.post(
                    this.URL + "frp/newTunnel",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                this.domainsRow = [];
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            let result = data;
            if(result?.data?.code != 1){
                this.$notify.error({ 
                    title: "提示",
                    message: result?.data?.msg,
                });
                return;
            }
            this.$notify.success({ 
                title: "提示",
                message: result?.data?.msg,
            });
            this.domainsRow = [];
            this.closeNew();
            this.closeServerSelectBox();
            //刷新数据
            this.getData(this.limit,this.page);
        },
        //打开新建弹窗
        newTunnel(data){
            this.isShowNew = true;
            this.editData.server_id = data.id;
            this.serverProtocos = [];
            data?.protocol.forEach(element => {
                this.serverProtocos.push({
                    value:element,
                    label:element
                });
            });
        },
        //关闭新建弹窗
        closeNew(){
            this.isShowNew = false;
            this.editData = {};
        },
        //关闭删除二次确认弹窗
        cancelDel(isExcuted=false){
            this.$refs?.confirm?.hide();
            if(isExcuted){
                this.isShowConfirm = false;
                this.delId = null;
                this.delName = "";
            }
        },
        //删除二次确认
        delConfirm(row){
            this.delId = row.id;
            this.delName = row.name;
            this.isShowConfirm = true;
        },
        //删除
        async del(){
            this.$root.loading(true);
            let data = null;
            let postData = {
                id: this.delId,
            };
            try {
                data = await axios.post(
                    this.URL + "frp/delTunnel",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            let result = data;
            if(result?.data?.code != 1){
                this.$notify.error({ 
                    title: "提示",
                    message: result?.data?.msg,
                });
                return;
            }
            this.$notify.success({ 
                title: "提示",
                message: result?.data?.msg,
            });
            //关闭弹窗
            this.cancelDel();
            //刷新数据
            this.getData(this.limit,this.page);
        },
        //快速禁用/启用
        async disable(row){
            this.$root.loading(true);
            let data = null;
            let postData = {
                id: row.id,
                name: row.name,
                localIP: row.local_addr,
                localPort: row.local_port,
                remark: row.remark,
                status: row.status=='normal'?'hidden':'normal'
            };
            try {
                data = await axios.post(
                    this.URL + "frp/editTunnel",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            let result = data;
            if(result?.data?.code != 1){
                this.$notify.error({ 
                    title: "提示",
                    message: result?.data?.msg,
                });
                return;
            }
            this.$notify.success({ 
                title: "提示",
                message: result?.data?.msg,
            });
            //刷新数据
            this.getData(this.limit,this.page);
        },
        //编辑保存
        async saveEdit() {
            this.$root.loading(true);
            let data = null;
            let postData = {
                id: this.editId,
                name: this.editData?.name,
                localIP: this.editData?.local_addr,
                localPort: this.editData?.local_port,
                remark: this.editData?.remark,
                status: this.editData?.status
            };
            try {
                data = await axios.post(
                    this.URL + "frp/editTunnel",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.$root.loading(false);
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.$root.loading(false);
            let result = data;
            if(result?.data?.code != 1){
                this.$notify.error({ 
                    title: "提示",
                    message: result?.data?.msg,
                });
                return;
            }
            this.$notify.success({ 
                title: "提示",
                message: result?.data?.msg,
            });
            this.closeEdit();
            //刷新数据
            this.getData(this.limit,this.page);
        },
        //关闭编辑窗
        closeEdit(){
            this.$root.enableMask(false);
            this.editId = null;
            this.editData = {};
        },
        //打开编辑窗
        edit(data) {
            //深拷贝 防止修改表格数据
            data = JSON.parse(JSON.stringify(data));
            this.editData = data;
            this.editId = data.id;
        },
        //翻页
        changePage(page){
            this.getData(this.limit,page);
        },
        //获取数据
        async getData(limit=10,page=1) {
            if(this.isInit){
                await this.getServerData();
            }            
            this.page = page;
            this.loading = true;
            this.tableData = [];
            // 协议处理
            let protocolFilterText = "";
            if (this.protocolFilter.length === 0) {
                this.protocolFilter = this.protocols.map(item => item.value);
            }
            protocolFilterText = this.protocolFilter.join(",");

            // 请求参数
            let postData = {
                protocol: protocolFilterText,
                name: this.nameFilter,
                local_addr: this.localAddrFilter,
                local_port: this.remoteAddrFilter,
                limit: limit,
                page: page,
            };
            let data = null;
            try {
                data = await axios.post(
                    this.URL + "frp/tunnelList",
                    postData,
                    {
                        headers: { token: localStorage.getItem("token") ?? "" },
                        signal: this.axiosC.signal
                    }
                );
            }
            catch (err) {
                this.loading = false;
                if ((axios.isCancel && axios.isCancel(err)) || err.name === 'CanceledError') {
                    console.log('用户取消请求');
                }
                else {
                    this.$root.netError(err);
                }
                return;
            }
            this.getUserInfo();
            this.loading = false;
            let result = data?.data?.data;
            this.totalPage = result?.last_page===0?1:result?.last_page;
            this.tableData = result?.data;
            this.isInit = false;
        },

        //列表搜索防抖
        debouncedGetData: (function () {
            let timer = null;
            return function () {
                if (timer) clearTimeout(timer);
                timer = setTimeout(() => {
                    this.getData(this.limit);
                }, 500);
            }
        })(),
        //服务器搜索防抖
        debouncedGetServersData: (function () {
            let timer = null;
            return function () {
                if (timer) clearTimeout(timer);
                timer = setTimeout(() => {
                    this.getServerData();
                }, 500);
            }
        })()
    },
    mounted() {
        this.$nextTick(() => {
            this.getData(this.limit);
        });
    },
    beforeDestroy() {
        this.axiosC.abort();
    }
}
</script>


