<template>
    <div :ref="'dialog_'+zIndex"
    class="dialog card"
    :style="buildStyle"
    >
        <div class="cardTitle" :style="'font-size:'+titleSize">
            <div class="text">{{ title }}</div>
            <closeLib v-if="isShowClose" size="3rem" @click="hide"/>
        </div>
        <div class="content">
            <slot></slot>
        </div>
    </div>
</template>

<script>
import closeLib from "@/components/lib/unit/closeLib.vue"

export default {
    name:"dialogLib",
    components:{
        closeLib
    },
    computed:{
        buildStyle(){
            let style = "";
            style += "width:"+this.width+";";
            style += "height:"+this.height+";";
            style += "max-height:"+this.maxHeight+";";
            style += "max-width:"+this.maxWidth+";";
            style += "z-index:"+this.zIndex+";";
            if(this.top != ''){
                style += "transform:translate(-50%, 0);";
                style += "top:"+this.top+";";
            }
            else{
                style += "transform:translate(-50%, -50%);"; 
            }
            return style;
        }
    },
    props:{
        title:{
            type:String,
            default:""
        },
        titleSize:{
            type:String,
            default:"1.4rem"
        },
        width:{
            type:String,
            default:"50%"
        },
        maxWidth:{
            type:String,
            default:"50%"
        },
        height:{
            type:String,
            default:"calc(70vh - 80px)"
        },
        maxHeight:{
            type:String,
            default:"70vh"
        },
        top:{
            type:String,
            default:""
        },
        isShowClose:{
            type:Boolean,
            default:true
        },
        hideWithMask:{
            type:Boolean,
            default:true
        }
    },
    data:function(){
        return {
            zIndex:8,
            maskClickHandler:()=>{
                let dialogs = document.querySelectorAll(".dialog");
                let max = 0, topDialog = null;
                dialogs.forEach(d => {
                    let z = parseInt(d.style.zIndex) || 0;
                    if (z > max) {
                    max = z;
                    topDialog = d;
                    }
                });

                // 只关掉最上层的 dialog
                if (this.$refs['dialog_' + this.zIndex] === topDialog) {
                    this.hide();
                }
            }
        }
    },
    methods:{
        //设置mask层监听器
        openListener(){
            let mask = document.querySelector(".mask");
            if (mask) {
                mask.addEventListener("click", this.maskClickHandler);
            }
        },
        hide(){
            let refName = 'dialog_' + this.zIndex;
            this.$refs[refName]?.classList.remove("active");
            //查找遮罩
            let mask = document.querySelector(".mask");
            if(mask != null){
                mask.style.zIndex = "";
            }
            if(this.hideWithMask){
                this.$root.enableMask(false);
                setTimeout(() => {
                    this.$emit('hide',true);    
                }, 300); 
                return;
            }    
            this.$emit('hide',true); 
        },
        show(){
            let refName = 'dialog_' + this.zIndex;
            this.$refs[refName]?.classList.add("active");
        },
        setIndex(){
            let dialogs = document.querySelectorAll(".dialog");
            let max = 0;
            for(let i = 0; i < dialogs.length; i++){
                let index = parseInt(dialogs[i].style.zIndex);
                if(index > max){
                    max = index;
                }
            }
            //查找遮罩
            let mask = document.querySelector(".mask");
            if(mask != null){
                mask.style.zIndex = max;
            }
            this.zIndex = max + 1;
        }
    },
    mounted() { 
        this.$nextTick(()=>{
            this.$root.enableMask(true);
            setTimeout(() => {
                this.show();
                this.openListener();
            }, 5);          
            this.setIndex(); 
        })
    },
    beforeDestroy() { 
        //清除mask监听器
        let mask = document.querySelector(".mask");
        if (mask && this.maskClickHandler) {
            mask.removeEventListener("click", this.maskClickHandler);
        }
    },
}

</script>

<style scoped lang="scss" src="@/assets/css/lib/unit/dialogLib.scss"></style>