<template>
    <div id="homePage">
        <div class="logo">
            <img :src="logo"/>
            <span>千鹤FRP</span>
        </div>
        <div class="backgroundBanner">
            <div class="bg" :style="'background-image:url('+h4+');'"></div>
            <!-- 切换方式1S -->
            <!-- <div class="bg" :style="'background-image:url('+h3+');'+'left:-'+(menuIndex==0?0:menuIndex-2)*100+'%'"></div>
            <div class="bg" :style="'background-image:url('+h2+');'+'left:-'+(menuIndex==0?0:menuIndex-1)*100+'%'"></div>
            <div class="bg" :style="'background-image:url('+h1+');'+'left:-'+menuIndex*100+'%'"></div> -->
            <!-- 切换方式1E -->

            <!-- 切换方式2S -->
            <div class="bg" :style="'background-image:url('+h3+');'+'opacity:'+(menuIndex==2?1:0)"></div>
            <div class="bg" :style="'background-image:url('+h2+');'+'opacity:'+(menuIndex==1?1:0)"></div>
            <div class="bg" :style="'background-image:url('+h1+');'+'opacity:'+(menuIndex==0?1:0)"></div>
            <!-- 切换方式2E -->

            <!-- 若要切换回纵向移动 将top改为0-100*...即可 -->
            <div class="mainContent h1Content" :style="'top:'+(0-20*(menuIndex==0?0:1))+'%;opacity:'+(menuIndex==0?1:0)+';z-index:'+(menuIndex==0?1:-1)">
                <img class="title" :src="font"/>
                <div class="btnGroup">
                    <div class="btn" @click="goManage()">
                        <div class="text">管理面板</div>
                    </div>
                    <div class="btn" @click="$root.goReg()">
                        <div class="text">注册账号</div>
                    </div>
                </div>
            </div>

            <!-- 切换方式1 -->
            <div class="mainContent h2Content" :style="'top:'+(0-20*(menuIndex==1?0:1))+'%;opacity:'+(menuIndex==1?1:0)+';z-index:'+(menuIndex==1?1:-1)">
                <div class="title">介绍</div>
                <div class="textBox" @mouseover="scrollLock=true" @mouseout="scrollLock=false">
                    <div class="text" v-html="h2Data.content??''"></div>
                </div>     
            </div>

            <!-- 切换方式1 -->
            <div class="mainContent h3Content" :style="'top:'+(0-20*(menuIndex==2?0:1))+'%;opacity:'+(menuIndex==2?1:0)+';z-index:'+(menuIndex==2?1:-1)">
                <div class="title">
                    <div class="text">定价</div>
                    <div class="more" @click="goMall()">更多套餐</div>
                </div>
                <div class="prices" @mouseover="scrollLock=true" @mouseout="scrollLock=false">
                    <div class="item" v-for="(item,key) in priceData" :key="key">   
                        <div class="name">{{ item.name }}</div>        
                        <div class="itemContent" v-html="item.content"></div>   
                        <div class="bottom">
                             <div class="price">价格:<span class="num">{{ item.price }}</span>/月</div>    
                             <div class="btn" @click="goMall(item)">
                                前去购买
                            </div>
                        </div>
                    </div>
                </div>
            </div>

            <div class="mainContent h4Content" :style="'top:'+(0-20*(menuIndex==3?0:1))+'%;opacity:'+(menuIndex==3?1:0)+';z-index:'+(menuIndex==3?1:-1)">
                <div class="title">团队</div>
                <div class="teamBox" @mouseover="scrollLock=true" @mouseout="scrollLock=false">
                    <div class="person" v-for="(item,key) in h4Data" :key="'team_'+key">
                        <div class="name">
                            <img :src="item.img"/>
                            {{item.name}}
                        </div>
                        <div class="job">
                            职责:{{ item.job }}
                        </div>
                        <div class="intro" v-html="item.content"></div>
                    </div>
                </div>       
            </div>

            <div class="menus">
                <div :class="menuIndex==key?'menu active':'menu'" v-for="(item,key) in menus" :key="key" @click="bannerClick(key)">
                    <div class="cn">{{ item.name }}</div>
                    <div class="en">{{ item.nameEn }}</div>
                </div>
            </div>
        </div>
        <div class="footer">
            千鹤FRP版权所有
        </div>
    </div>    
</template>

<script>
import h1 from "@/assets/img/h1.jpg"
import h2 from "@/assets/img/h2.jpg"
import h3 from "@/assets/img/h3.jpg"
import h4 from "@/assets/img/h4.jpg"
import font from "@/assets/img/font.png"
import logo from "@/assets/img/logo.png"
import axios from "axios"

export default {
  name: 'HomePage',
  components: {
  }, 
  data() {
    return {
        h1:h1,
        h2:h2,
        h3:h3,
        h4:h4,
        font:font,
        logo:logo,
        URL:window.config.apiUrl,
        scrollLock:false,
        menus:[
            {name:"首页",nameEn:"Home Page",background:h1},
            {name:"介绍",nameEn:"Introduce",background:h2},
            {name:"定价",nameEn:"Pricing",background:h3},
            {name:"团队",nameEn:"Team",background:h1},
        ],
        menuIndex:0,
        priceData:[],
        h2Data:[],
        h4Data:[],
    }
  },
  methods: {
    bannerClick(index=0){
        this.menuIndex = index;
    },
    //滚轮监听
    scrollListener() {
        let isScrolling = false;

        window.addEventListener('wheel', (e) => {
            if (isScrolling || this.$root.scrollLock || this.scrollLock) return; // 忽略连续滚动或滚动锁定
            if (e.deltaY < 0) {
            this.menuIndex = this.menuIndex === 0 ? this.menus.length - 1 : this.menuIndex - 1;
            } else {
            this.menuIndex = this.menuIndex === this.menus.length - 1 ? 0 : this.menuIndex + 1;
            }

            isScrolling = true;
            setTimeout(() => {
            isScrolling = false;
            }, 300);
        });
    },
    //请求套餐数据
    getPrice(page=1,limit=3){
        axios.get(this.URL+"content/package?page="+page+"&limit="+limit)
        .then(res=>{ 
            this.priceData = res.data.data.data ?? [];
        })
        .catch(err=>{ 
            this.$root.netError(err);
        });
    },
    //请求内容文本数据
    getData(){
        axios.get(this.URL+"content/front")
        .then(res=>{ 
            let data = res.data.data ?? [];
            this.h2Data = data.intro[0] ?? [];
            this.h4Data = data.team ?? [];
        })
        .catch(err=>{ 
            this.$root.netError(err);
        });
    },
    //前往商城页
    async goMall(packageItem = null){
        let isLogin = await this.$root.checkLogin();
        if(isLogin){
            if(this.$route.path != "/mall"){
                // 如果有套餐信息，通过query参数传递
                if(packageItem) {
                    this.$router.replace({
                        path: "/mall",
                        query: {
                            package: packageItem.name,
                            price: packageItem.price
                        }
                    });
                } else {
                    this.$router.replace("/mall");
                }
            }
            return;
        }else{
            this.$root.goLogin();
        }
    },
    //前往管理面板
    async goManage(){
        let isLogin = await this.$root.checkLogin();
        if(isLogin){
            if(this.$route.path != "/usercenter"){
                this.$router.replace("/usercenter");
            }
            return;
        }else{
            this.$root.goLogin();
        }
    }
  },
  beforeCreate(){
  },
  created(){
    this.getPrice();
    this.getData();
  },
  mounted(){
    this.$nextTick(() => { 
        this.scrollListener();
    });
  }
}
</script>

<style scoped lang="scss" src="@/assets/css/view/homePage.scss"></style>